import {
  getSessionStorage
} from '@/utils/base'
import {
  createdAuth
} from '@/utils/createdAuth'

const filters = {

  /**/
  testFilter:function(val){
    console.log(val);
    return val%2==0?true:false;
  },

  /**/
  isAuth:function(val){
    let auth=getSessionStorage('authlist_supplier');
    if(!auth){
      let authlist={}
      createdAuth(auth,authlist);
      setSessionStorage('authlist_supplier', authlist);
      auth=authlist;
    }
    let value=val.toLowerCase();
    if(auth[value]!=true){
      return false;
    }else{
      return true;
    }
  },

	/*-*/
	isNull:function(val){

		if(val==null||val==undefined||val===""||val==="null"||val=='undefined'){
			return '-';
		}else if(val==0 || val=='0'){
			return val;
		}
		else{
			return val;
		}
	},

	/**/
	returnPercentage:function(val){
		if(val!==null&&val!==''&&val!==undefined){
			let num=(val*100).toFixed(2);
			return num+'%';
		}else{
			return '-';
		}
	},

	/**/
	returnToFixed:function(val,num){
		if(val!=null){
			let nums=val.toFixed(num);
			return nums;
		}else{
			if(val!=null&&val!==""){
				return val;
			}else{
				return '-';
			}
		}
	},

	/**/
	tenThousand:function(val){
		if(val!=null&&val!=''){
			var x=(val/10000).toFixed(2);
			var f = Math.round(x * 100) / 100;
   			var s = f.toString();
			return s;
		}else{
			return '-';
		}
	},

	/**/
	numTabWeek: function(val) {

		let ch = '';
		switch(val) {
			case 1:
				ch = '';
				break;
			case 2:
				ch = '';
				break;
			case 3:
				ch = '';
				break;
			case 4:
				ch = '';
				break;
			case 5:
				ch = '';
				break;
			case 6:
				ch = '';
				break;
			case 7:
				ch = '';
				break;
		}
		return ch;

	},

	/**/
	convertSex: function(num){
		let sex = '';
		switch(num) {
			case 0:
				sex = '';
				break;
			case 1:
				sex= '';
				break;
			default:
				sex = '';
		}
		return sex;
	}


}

export default filters;



