import request from '@/utils/request'

let PointsApi = {
   /**/
   activeList(data, errorback) {
     return request._post('/supplier/activity.point/index', data, errorback);
   },
   getProduct(data, errorback){
      return request._get('/supplier/activity.point/add', data, errorback);
   },
   addProduct(data, errorback){
      return request._post('/supplier/activity.point/add', data, errorback);
   },
   /**/
   myList(data, errorback) {
     return request._post('/supplier/activity.point/my', data, errorback);
   },
   /**/
   detailProduct(data, errorback){
      return request._get('/supplier/activity.point/edit', data, errorback);
   },
   /**/
   saveProduct(data, errorback){
      return request._post('/supplier/activity.point/edit', data, errorback);
   },
   delProduct(data, errorback){
       return request._post('/supplier/activity.point/del', data, errorback);
   },
}
export default PointsApi;



