<template>
  <!--
       luoyiming
      2019-10-26
      -
  -->
  <div class="user">
    <div class="common-form"></div>
    <!---->
    <div class="common-level-rail">
      <el-button size="small" type="primary" @click="addClick" v-auth="'/setting/printer/add'"></el-button>
    </div>

    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table size="small" :data="tableData" border style="width: 100%" v-loading="loading">
          <el-table-column prop="printer_id" label="ID"></el-table-column>
          <el-table-column prop="printer_name" label=""></el-table-column>
          <el-table-column prop="printer_type" label="	"></el-table-column>
          <el-table-column prop="sort" label=""></el-table-column>
          <el-table-column prop="create_time" label=""></el-table-column>
          <el-table-column fixed="right" label="" width="90">
            <template slot-scope="scope">
              <el-button @click="editClick(scope.row)" type="text" size="small" v-auth="'/setting/printer/edit'"></el-button>
              <el-button @click="deleteClick(scope.row)" type="text" size="small" v-auth="'/setting/printer/delete'"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!---->
      <div class="pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background :current-page="curPage"
          :page-size="pageSize" layout="total, prev, pager, next, jumper"
          :total="totalDataNumber">
        </el-pagination>
      </div>
    </div>

  </div>
</template>

<script>
  import SettingApi from '@/api/setting.js';

  export default {
    data() {
      return {
        /**/
        loading: true,
        /**/
        tableData: [],
        /**/
        pageSize: 20,
        /**/
        totalDataNumber: 0,
        /**/
        curPage: 1,
        /**/
        formInline: {
          user: '',
          region: ''
        },
        /**/
        open_add: false,
        /**/
        open_edit: false,
        /**/
        userModel: {}
      };
    },
    created() {

      /**/
      this.getData();

    },
    methods: {

      /**/
      handleCurrentChange(val) {
        let self = this;
        self.loading = true;
        self.curPage = val;
        self.getData();
      },

      /**/
      handleSizeChange(val) {
        this.curPage = 1;
        this.pageSize = val;
        this.getData();
      },

      /**/
      getData() {
        let self = this;
        let Params = {};
        Params.page = self.curPage;
        Params.list_rows = self.pageSize;
        SettingApi.printerList(Params, true)
          .then(data => {
            self.loading = false;
            self.tableData = data.data.list.data;
            self.totalDataNumber = data.data.list.total
          })
          .catch(error => {

          });
      },
      /**/
      addClick() {
        this.$router.push('/setting/printer/add');
      },

      /**/
      editClick(item) {
        let self = this;
        this.$router.push({
          path: '/setting/printer/edit',
          query: {
            printer_id: item.printer_id
          }
        })
      },


      /**/
      deleteClick(row) {
        let self = this;
        self.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          self.loading = true;
          SettingApi.deletePrinter({
              printer_id: row.printer_id
            }, true).then(data => {
              self.loading = false;
              self.$message({
                message: data.msg,
                type: 'success'
              });
              self.getData();
            })
            .catch(error => {
              self.loading = false;
            });

        }).catch(() => {
          self.loading = false;
        });
      }

    }
  };
</script>

<style></style>



