<template>
  <!--
    	luoyiming
    	2019-10-26
    	--
    -->
  <div class="product-add">
    <!--form-->
    <el-form size="small" ref="form" :model="form" label-width="200px">
      <!---->
      <div class="common-form"></div>

      <el-form-item label="">
        <el-row>
          <el-button @click="addClick" icon="el-icon-plus"></el-button>
          <div v-if="form.user_id!=''" class="img">
            <img :src="user_info.user.avatarUrl" width="100" height="100" />
          </div>
          <div>
            <el-link :underline="false" disabled></el-link>
          </div>
        </el-row>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="form.store_id" placeholder="">
          <el-option v-for="(item,index) in storeList" :key="index" :label="item.store_name" :value="item.store_id"></el-option>
        </el-select>
        <div>
          <el-link :underline="false" disabled></el-link>
        </div>
      </el-form-item>
      <el-form-item label="" prop="real_name" :rules="[{required: true,message: ''}]">
        <el-input class="max-w460" v-model="form.real_name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="mobile" :rules="[{required: true,message: ''}]">
        <el-input class="max-w460" v-model="form.mobile" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-radio-group v-model="form.status">
          <el-radio :label="1"></el-radio>
          <el-radio :label="0"></el-radio>
        </el-radio-group>
      </el-form-item>
      <!---->
      <div class="common-button-wrapper">
        <el-button size="small" type="info" @click="cancelFunc"></el-button>
        <el-button size="small" type="primary" @click="onSubmit" :loading="loading"></el-button>
      </div>
    </el-form>

    <!---->
    <GetUser :is_open="open_add" @close="closeDialogFunc"></GetUser>

  </div>
</template>

<script>
  import StoreApi from '@/api/store.js';
  import GetUser from '@/components/user/GetUser.vue';
  export default {
    components: {
      /**/
      GetUser
    },
    data() {
      return {
        /*form*/
        form: {
          user_id: '',
          sotre_id: '',
          status: 1,
          real_name: '',
          mobile: '',
        },
        user_info: {},
        /**/
        open_add: false,
        storeList: [],
        /**/
        loading:false
      };
    },
    created() {
      /**/
      this.getTableList();
    },

    methods: {


      /**/
      onSubmit() {
        let self = this;
        let form = self.form;
        self.$refs.form.validate((valid) => {
          if (valid) {
            self.loading = true;
            StoreApi.addClerk(form, true)
              .then(data => {
                self.loading = false;
                if (data.code == 1) {
                  self.$message({
                    message: '',
                    type: 'success'
                  });
                  self.$router.push('/store/clerk/index');
                } else {
                  self.loading = false;
                }
              })
              .catch(error => {
                self.loading = false;
              });
          }
        });


      },

      /**/
      getTableList() {
        StoreApi.storeLists('', true)
          .then(res => {
            this.storeList = res.data.list;
          })
          .catch(error => {

          });
      },

      /**/
      addClick() {
        this.open_add = true;
      },

      /**/
      closeDialogFunc(e) {
        if (e.type != 'error') {
          this.user_info = e.params[0];
          console.log(this.user_info);
          this.form.user_id = e.params[0].user_id;
        }
        this.open_add = false;
      },

      /**/
      cancelFunc() {
        this.$router.back(-1);
      }


    }

  };
</script>

<style lang="scss" scoped>
  .basic-setting-content {}

  .product-add {
    padding-bottom: 50px;
  }

  .img {
    margin-top: 10px;
  }
</style>



