<template>
  <!--
          luoyiming
          2019-10-25
          
      -->
  <div class="user">
    <!---->
    <!-- <div class="common-level-rail"><el-button size="small" type="primary" icon="el-icon-plus" @click="addClick" v-auth="'/store/store/add'"></el-button></div> -->

    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table size="small" :data="tableData" border style="width: 100%" v-loading="loading">
          <el-table-column prop="user_id" label="ID" width="160"></el-table-column>
          <el-table-column prop="user.nickName" label="">
            <template slot-scope="scope">
              <div v-if="scope.row.msg_type==1">
                <span>{{scope.row.supplier.name}}</span>
              </div>
              <div v-else><span>{{scope.row.user.nickName}}</span></div>
            </template>
          </el-table-column>
          <el-table-column label="" width="100">
            <template slot-scope="scope">
              <div v-if="scope.row.msg_type==1">
                <img v-img-url="scope.row.supplier.logo.file_path" width="30px" height="30px" />
              </div>
              <div v-else><img v-img-url="scope.row.user.avatarUrl" width="30px" height="30px" /></div>

            </template>
          </el-table-column>
          <el-table-column prop="content" label="">
            <template slot-scope="scope">
              <div v-if="scope.row.type==0">
                <span>{{scope.row.content}}</span>
              </div>
              <div v-if="scope.row.type==1">
                <img v-img-url="scope.row.content" width="100px" height="100px" />
              </div>
              <div v-if="scope.row.type==2">
                <img v-img-url="JSON.parse(scope.row.content).product_img" width="50px" height="50px" />
                <div class="pro_txtname nmaxwidth">{{JSON.parse(scope.row.content).product_name}}</div>
                <div class="pro_price">{{JSON.parse(scope.row.content).product_price}}</div>
              </div>
              <div class="d-s-c" v-if="scope.row.type==3">
                <div>
                  <img v-img-url="JSON.parse(scope.row.content).product_img" width="50px" height="50px" />
                </div>
                <div>
                  <div class="pro_txtname nmaxwidth">{{JSON.parse(scope.row.content).order_no}}</div>
                  <div class="pro_txtname nmaxwidth">:{{JSON.parse(scope.row.content).product_name}}</div>
                  <div class="pro_price">:{{JSON.parse(scope.row.content).order_price}}</div>
                  <div class="pro_price">{{JSON.parse(scope.row.content).order_num}}</div>
                  <div class="pro_txtname nmaxwidth">{{JSON.parse(scope.row.content).create_time}}</div>
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="create_time" label="" width="140"></el-table-column>
          <!-- <el-table-column fixed="right" label="" width="190">
            <template slot-scope="scope">
              <el-button @click="editClick(scope.row)" type="text" size="small" v-auth="'/store/store/edit'"></el-button>
              <el-button @click="deleteClick(scope.row)" type="text" size="small" v-auth="'/store/store/delete'"></el-button>
            </template>
          </el-table-column> -->
        </el-table>
      </div>

      <!---->
      <div class="pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background :current-page="curPage"
          :page-size="pageSize" layout="total, prev, pager, next, jumper" :total="totalDataNumber"></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  import ServiceApi from '@/api/service.js';
  export default {
    components: {},
    inject: ['reload'],
    data() {
      return {
        /**/
        loading: true,
        /**/
        tableData: [],
        /**/
        pageSize: 20,
        /**/
        totalDataNumber: 0,
        /**/
        curPage: 1,
        /**/
        formInline: {
          user: '',
          region: ''
        },
        /**/
        open_add: false,
        /**/
        open_edit: false,
        /**/
        userModel: {},
        user_id: 0
      };
    },
    created() {
      this.user_id = this.$route.query.user_id;
      /**/
      this.getTableList();
    },
    methods: {
      /**/
      handleCurrentChange(val) {
        let self = this;
        self.curPage = val;
        self.loading = true;
        self.getTableList();
      },

      /**/
      handleSizeChange(val) {
        this.curPage = 1;
        this.pageSize = val;
        this.getTableList();
      },

      /**/
      getTableList() {
        let self = this;
        let Params = {};
        Params.user_id = self.user_id;
        Params.page = self.curPage;
        Params.list_rows = self.pageSize;
        ServiceApi.recordlist(Params, true)
          .then(data => {
            self.loading = false;
            self.tableData = data.data.list.data;
            self.totalDataNumber = data.data.list.total;
          })
          .catch(error => {});
      },

      /**/
      addClick() {
        this.$router.push('/store/store/add');
      },

      /**/
      editClick(row) {
        let self = this;
        let params = row.store_id;
        this.$router.push({
          path: '/store/store/edit',
          // name: 'mallList',
          query: {
            store_id: params
          }
        });
      },

      /**/
      deleteClick(row) {
        let self = this;
        self
          .$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          })
          .then(() => {
            ServiceApi.deleteShop({
                  store_id: row.store_id
                },
                true
              )
              .then(data => {
                self.$message({
                  message: '',
                  type: 'success'
                });
                self.getTableList();
              })
              .catch(error => {});
          })
          .catch(() => {});
      }
    }
  };
</script>

<style>
  .nmaxwidth{
    max-width: 250px;
  }
</style>



