<template>
    <div>
      <!--form-->
      <el-form size="small" ref="formData" :model="formData" label-width="150px">
        <el-form-item label="">
          <div>
            <el-radio v-model="formData.service_type" :label="10"></el-radio>
            <el-radio v-model="formData.service_type" :label="20" v-if="formData.service_open==1"></el-radio>
          </div>
        </el-form-item>
        <div v-if="formData.service_type==10">
        <el-form-item label="">
          <el-input v-model="formData.wechat" placeholder="" class="max-w460"></el-input>
        </el-form-item>
        <el-form-item label="qq">
          <el-input v-model="formData.qq" placeholder="qq" class="max-w460"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formData.phone" placeholder="" class="max-w460"></el-input>
        </el-form-item>
        </div>
        <el-form-item label="" v-if="formData.service_type==20" :rules="[{required: true,message: 'id'}]" prop="user_id">
          <el-input type="number" v-model="formData.user_id" placeholder="id" class="max-w460" :disabled="user_id>0"></el-input>
          <p class="gray">ID</p>
        </el-form-item>
        <!---->
        <div class="common-button-wrapper">
          <el-button size="small" type="primary" @click="onSubmit" :disabled="loading"></el-button>
        </div>
      </el-form>
    </div>
  </template>

  <script>
    import SettingApi from '@/api/setting.js';
    export default{
      data(){
        return {
          /**/
          loading:false,
          /**/
          formData:{
            service_type: 10,
            wechat:'',
            qq:'',
            phone:'',
            service_open:'',
            user_id:''
          },
          user_id:0
        }
      },
      created() {
        this.getData();
      },
      methods:{
        /**/
        getData() {
          let self = this;
          SettingApi.getService({}, true).then(res => {
              if(res.data.model != null){
                self.formData = res.data.model;
                self.user_id = res.data.model.user_id;
              }
            })
            .catch(error => {

            });
        },

        /**/
        onSubmit() {
          let self = this;
          let params = this.formData;
          self.$refs.formData.validate((valid) => {
            if (valid) {
              self.loading = true;
              SettingApi.setService(params, true)
                .then(data => {
                  self.loading = false;
                  self.$message({
                    message: '',
                    type: 'success'
                  });
                })
                .catch(error => {
                  self.loading = false;
                });
            }
          });
        },
      }
    }
  </script>

  <style>
  </style>



