/**/
let baserouter = [
    /**/
    {
        path: '/login',
        name: 'login',
        meta: {
            title: ''
        },
        component: () =>
            import('@/views/login')
    },
    {
        path: '/',
        redirect: {
            name: 'Home'
        },
        meta: {
            title: ''
        },
        component: () =>
            import('@/views/layout/main'),
        children: [
            /**/
            {
                path: '/home',
                name: 'Home',
                meta: {
                    title: ''
                },
                component: () =>
                    import('@/views/home/home')
            },
        ]
    },
];

/**/
export const errpage = [{
  path: '*',
  name: 'Page404',
  meta: {
    title: ''
  },
  component: () =>
    import('@/views/error-page/404')
}]

export default baserouter;



