<template>
  <!--
      	luoyiming
      	2019-06-04
      	--
      -->
  <div class="user">
    <!---->
    <div class="common-seach-wrap">
    </div>
    <div class="common-level-rail">
      <el-button size="small" type="primary" icon="el-icon-plus" @click="addClick"></el-button>
    </div>
    <div class="product-content">
      <el-form ref="form" :model="form" label-width="100px">
        <div class="table-wrap">
          <el-table size="small" :data="tableData" border style="width: 100%" v-loading="loading">
            <el-table-column prop="coupon_id" label="ID" width="70"></el-table-column>
            <el-table-column prop="coupon_type" label="" width="100"></el-table-column>
            <el-table-column prop="name" label=""></el-table-column>
            <el-table-column prop="min_price" label=""></el-table-column>
            <el-table-column prop="seckill_stock" label="">
              <template slot-scope="scope">
                <div v-if="scope.row.coupon_type.value==10">
                  <span> <strong class="orange">{{scope.row.reduce_price}}</strong> </span>
                </div>
                <div v-if="scope.row.coupon_type.value==20">
                  <span> <strong class="orange">{{scope.row.discount}}</strong> </span>
                </div>
              </template>
            </el-table-column>

            <el-table-column prop="seckill_stock" label="">
              <template slot-scope="scope">
                <div v-if="scope.row.expire_type==10">
                  <span> <strong>{{scope.row.expire_day}}</strong> </span>
                </div>
                <div v-if="scope.row.expire_type==20">
                  <span>
                    {{scope.row.start_time.text}}
                    ~
                    {{scope.row.end_time.text}}
                  </span>
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="receive_num" label="">
              <template slot-scope="scope">
                <div v-if="scope.row.total_num==-1">
                  <span></span>
                </div>
                <div v-else>
                  <span>{{scope.row.total_num}}</span>
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="receive_num" label=""></el-table-column>
            <el-table-column prop="sort" label="" ></el-table-column>
            <el-table-column prop="create_time" label="" width="135"></el-table-column>
            <el-table-column fixed="right" label="" width="90">
              <template slot-scope="scope">

                <el-button @click="editClick(scope.row)" type="text" size="small"></el-button>
                <el-button @click="deleteClick(scope.row)" type="text" size="small"></el-button>


              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-form>
      <!---->
      <div class="pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background :current-page="curPage"
           :page-size="pageSize" layout="total, prev, pager, next, jumper"
          :total="totalDataNumber">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
  import CouponApi from '@/api/coupon.js';
  export default {
    data() {
      return {
        formInline: {},
        form: {},
        tableData: [],
        /**/
        pageSize: 20,
        /**/
        totalDataNumber: 0,
        /**/
        curPage: 1,
        /**/
        loading: true,
      };
    },
    created() {
      /**/
      this.getData();
    },
    methods: {
      /**/
      getData() {
        let self = this;
        let Params = {};
        Params.page = self.curPage;
        Params.list_rows = self.pageSize;
        CouponApi.couponList(Params, true)
          .then(data => {
            self.loading = false;
            self.tableData = data.data.list.data;
            self.totalDataNumber = data.data.list.total
          })
          .catch(error => {});
      },
      /**/
      handleCurrentChange(val) {
        let self = this;
        self.curPage = val;
        self.loading = true;
        self.getData();
      },

      /**/
      handleSizeChange(val) {
        this.curPage = 1;
        this.pageSize = val;
        this.getTableList();
      },
      /**/
      addClick() {
        this.$router.push('/coupon/coupon/add');
      },
      /**/
      editClick(e) {
        let self = this;
        this.$router.push({
          path: '/coupon/coupon/edit',
          query: {
            coupon_id: e.coupon_id
          }
        })
      },
      /**/
      deleteClick(e) {
        let self = this;
        self.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          self.loading = true;
          CouponApi.deleteCoupon({
              coupon_id: e.coupon_id
            }, true).then(data => {
              self.loading = false;
              self.$message({
                message: data.msg,
                type: 'success'
              });
              self.getData();

            })
            .catch(error => {
              self.loading = false;
            });

        }).catch(() => {
          self.loading = false;
        });
      }

    }
  };
</script>

<style>
</style>



