<template>
  <!--
        luoyiming
        2019-10-25
        -
    -->
  <div class="user">

    <!---->
    <div class="common-seach-wrap">
      <el-form size="small" :inline="true" :model="searchForm" class="demo-form-inline">
        <el-form-item><el-input size="small" v-model="searchForm.search" placeholder=""></el-input></el-form-item>
        <el-form-item>
          <el-button size="small" type="primary" icon="el-icon-search" @click="searchSubmit"></el-button>
        </el-form-item>
      </el-form>
    </div>

    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table size="small" :data="tableData" border style="width: 100%" v-loading="loading">
          <el-table-column prop="login_log_id" label="ID"></el-table-column>
          <el-table-column prop="ip" label="IP"></el-table-column>
          <el-table-column prop="result" label=""></el-table-column>
          <el-table-column prop="username" label=""></el-table-column>
          <el-table-column prop="create_time" label=""></el-table-column>
        </el-table>
      </div>

      <!---->
      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          background
          :current-page="curPage"
          :page-size="pageSize"
          layout="total, prev, pager, next, jumper"
          :total="totalDataNumber"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import AuthApi from '@/api/auth.js';
export default {
  components: {},
  inject: ['reload'],
  data() {
    return {
      /**/
      loading: true,
      /**/
      tableData: [],
      /**/
      pageSize: 20,
      /**/
      totalDataNumber: 0,
      /**/
      curPage: 1,
      /**/
      searchForm: {
        search:''
      },
      /**/
      open_add: false,
      /**/
      open_edit: false,
      /**/
      userModel: {}
    };
  },
  created() {
    /**/
    this.getTableList();
  },
  methods: {

    /**/
    searchSubmit(){
      this.curPage = 1;
      this.getTableList();
    },

    /**/
    handleCurrentChange(val) {
      let self = this;
      self.curPage = val;
      self.loading = true;
      self.getTableList();
    },

    /**/
    handleSizeChange(val) {
      this.curPage = 1;
      this.pageSize = val;
      this.getTableList();
    },

    /**/
    getTableList() {
      let self = this;
      let Params = {
        page: self.curPage,
        list_rows: self.pageSize,
        username: self.searchForm.search
      };

      AuthApi.loginlog(Params, true)
        .then(data => {
          self.loading = false;
          self.tableData = data.data.list.data;
          self.totalDataNumber = data.data.list.total;
        })
        .catch(error => {});
    },

    /**/
    addClick() {
      this.$router.push('/auth/user/add');
    },

    /**/
    editClick(row) {
      let self = this;
      this.$router.push({
        path: '/auth/user/edit',
        query: {
          shop_user_id: row.shop_user_id
        }
      });
    },

    /**/
    refresh() {
      this.reload();
      // reload
    },

    /**/
    deleteClick(row) {
      let self = this;
      self
        .$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
        .then(() => {
          self.loading = true;
          AuthApi.userDelete(
            {
              shop_user_id: row.shop_user_id
            },
            true
          )
            .then(data => {
              self.loading = false;
              if (data.code == 1) {
                self.$message({
                  message: '',
                  type: 'success'
                });
                //
                self.getTableList();
              } else {
                self.loading = false;
              }
            })
            .catch(error => {
              self.loading = false;
            });
        })
        .catch(() => {});
    }
  }
};
</script>

<style></style>



