<template>
  <div class="product-list">
    <!---->
    <div class="common-seach-wrap">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="sell">
          <span slot="label"> <el-tag size="mini">{{product_count.sell}}</el-tag></span>
        </el-tab-pane>
        <el-tab-pane label="" name="lower">
          <span slot="label"> <el-tag size="mini">{{product_count.lower}}</el-tag></span>
        </el-tab-pane>
        <el-tab-pane label="" name="recovery">
          <span slot="label"> <el-tag size="mini">{{product_count.recovery}}</el-tag></span>
        </el-tab-pane>
        <el-tab-pane label="" name="stock">
          <span slot="label"> <el-tag size="mini">{{product_count.stock}}</el-tag></span>
        </el-tab-pane>
        <el-tab-pane label="" name="draft">
          <span slot="label"> <el-tag size="mini">{{product_count.draft}}</el-tag></span>
        </el-tab-pane>
        <el-tab-pane label="" name="audit">
          <span slot="label"> <el-tag size="mini">{{product_count.audit}}</el-tag></span>
        </el-tab-pane>
        <el-tab-pane label="" name="no_audit">
          <span slot="label"> <el-tag size="mini">{{product_count.no_audit}}</el-tag></span>
        </el-tab-pane>
      </el-tabs>
      <el-form size="small" :inline="true" :model="searchForm" class="demo-form-inline">
        <el-form-item label="">
          <el-select size="small" v-model="searchForm.category_id" placeholder="">
            <el-option label="" value="0"></el-option>
            <el-option v-for="(item, index) in categoryList" :key="index" :label="item.name" :value="item.category_id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=""><el-input size="small" v-model="searchForm.product_name" placeholder=""></el-input></el-form-item>
        <el-form-item>
          <el-button size="small" type="primary" icon="el-icon-search" @click="onSubmit"></el-button>
        </el-form-item>
      </el-form>
    </div>

    <!---->
    <div class="common-level-rail" v-if="supplier.status==0"><el-button size="small" type="primary" icon="el-icon-plus" @click="addClick" v-auth="'/product/product/add'"></el-button></div>
    <div class="common-level-rail" v-if="supplier.status!=0">
      <el-button size="small" type="primary" icon="el-icon-plus" v-auth="'/product/product/add'" disabled="true"></el-button>
      <span class="red pl16" v-if="supplier.status==10"></span>
      <span class="red pl16" v-if="supplier.status==20"></span>
    </div>
    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table size="small" :data="tableData" border style="width: 100%" v-loading="loading">
          <el-table-column prop="product_name" label="" width="400px">
            <template slot-scope="scope">
              <div class="product-info">
                <div class="pic"><img v-img-url="scope.row.image[0].file_path" alt="" /></div>
                <div class="info">
                  <div class="name">{{ scope.row.product_name }}</div>
                  <div class="price">{{ scope.row.product_price }}</div>
                  <div class="price" v-if="scope.row.audit_status == 20">{{scope.row.audit_remark}}</div>
                  <div class="price" v-if="scope.row.audit_status == 30">{{scope.row.audit_remark}}</div>
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="category.name" label=""></el-table-column>
          <el-table-column prop="sales_actual" label=""></el-table-column>
          <el-table-column prop="product_stock" label=""></el-table-column>
          <el-table-column prop="view_times" label=""></el-table-column>
          <el-table-column prop="product_status.text" label="">
            <template slot-scope="scope">
              <span v-if="scope.row.is_virtual == 0"></span>
              <span v-if="scope.row.is_virtual == 1" class="green"></span>
            </template>
          </el-table-column>
          <el-table-column prop="create_time" label=""></el-table-column>
          <el-table-column prop="product_sort" label=""></el-table-column>
          <el-table-column fixed="right" label="" width="80">
            <template slot-scope="scope" v-if="supplier.status==0">
              <div class="table-btn-column">
                <el-button @click="editClick(scope.row)" type="text" size="small" v-auth="'/product/product/edit'"></el-button>
                <el-button @click="delClick(scope.row)" type="text" size="small" v-auth="'/product/product/delete'"></el-button>
                <el-button v-if="activeName == 'sell'" @click="changeStatus(scope.row, 20)" type="text" size="small" v-auth="'/product/product/edit'"></el-button>
                <el-button v-if="activeName == 'lower'" @click="changeStatus(scope.row, 10)" type="text" size="small" v-auth="'/product/product/edit'"></el-button>
                <el-button @click="copyClick(scope.row)" type="text" size="small" v-auth="'/product/product/copy'"></el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <!---->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        background
        :current-page="curPage"
        :page-size="pageSize"
        layout="total, prev, pager, next, jumper"
        :total="totalDataNumber"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import PorductApi from '@/api/product.js';
export default {
  components: {},
  data() {
    return {
      /**/
      activeName: 'sell',
      /**/
      activeIndex: '0',
      /**/
      loading: true,
      /**/
      pageSize: 10,
      /**/
      totalDataNumber: 0,
      /**/
      curPage: 1,
      /**/
      searchForm: {
        product_name: '',
        category_id: ''
      },
      /**/
      tableData: [],
      /**/
      categoryList: [],
      supplier:[],
      /**/
      product_count: {}
    };
  },
  created() {
    /**/
    this.getData();
  },
  methods: {
    /**/
    handleCurrentChange(val) {
      let self = this;
      self.loading = true;
      self.curPage = val;
      self.getData();
    },

    /**/
    handleSizeChange(val) {
      this.pageSize = val;
      this.getData();
    },

    /**/
    handleClick(tab, event) {
      let self = this;
      self.curPage = 1;
      self.getData();
    },

    /**/
    getData() {
      let self = this;
      let Params = self.searchForm;
      Params.page = self.curPage;
      Params.list_rows = self.pageSize;
      Params.type = self.activeName;
      self.loading = true;
      PorductApi.productList(Params, true)
        .then(data => {
          self.loading = false;
          self.tableData = data.data.list.data;
          self.categoryList = data.data.category;
          self.totalDataNumber = data.data.list.total;
          self.supplier = data.data.supplier;
          self.product_count = data.data.product_count;
        })
        .catch(error => {
          self.loading = false;
        });
    },

    /**/
    onSubmit() {
      this.curPage = 1;
      this.getData();
    },

    /**/
    addClick() {
      this.$router.push('/product/product/add');
    },

    /**/
    editClick(row) {
      this.$router.push({
        path: '/product/product/edit',
        query: {
          product_id: row.product_id,
          scene: 'edit'
        }
      });
    },

    /**/
    copyClick(row) {
      this.$router.push({
        path: '/product/product/edit',
        query: {
          product_id: row.product_id,
          scene: 'copy'
        }
      });
    },

    /**/
    delClick: function(row) {
      let self = this;
      let msg = '';
      if(row.product_status == 30){
        msg = '?';
      }else{
        msg = '?';
      }
      self
        .$confirm(msg, '', {
          type: 'warning'
        })
        .then(() => {
          PorductApi.delProduct({
            product_id: row.product_id
          }).then(data => {
            self.$message({
              message: '',
              type: 'success'
            });
            self.getData();
          });
        });
    },
    /**/
    changeStatus(item,value){
      let self = this;
      let msg = '';
      if(value == 10){
        msg = '?';
      }else{
        msg = '?';
      }
      self.$confirm(msg, '', {
          type: 'warning'
        })
        .then(() => {
          PorductApi.changeStatus({
            product_id: item.product_id,
            state: value
          }).then(data => {
            self.$message({
              message: '',
              type: 'success'
            });
            self.getData();
          });
        });
    },
  }
};
</script>

<style></style>



