import request from '@/utils/request'
let OrderApi = {
  /**/
  orderlist(data, errorback) {
    return request._post('/supplier/order.order/index', data, errorback);
  },
   // 
   withdrawalList(data, errorback) {
    return request._post('/supplier/withdrawal.withdrawal/withdrawalList', data, errorback);
  },
  // 
  withdrawalAudit(data, errorback) {
    return request._post('/supplier/withdrawal.withdrawal/withdrawalAudit', data, errorback);
  },
  // 
  returnReview(data, errorback) {
    return request._post('/supplier/order.order/returnReview', data, errorback);
  },
  toExamine(data, errorback) {
    return request._post('/supplier/order.Operate/toExamine', data, errorback);
  },
  /**/
  orderdetail(data, errorback) {
    return request._post('/supplier/order.order/detail', data, errorback);
  },
  /**/
  orderrefund(data, errorback) {
    return request._post('/supplier/order.refund/index', data, errorback);
  },
  /**/
  delivery(data, errorback) {
    return request._post('/supplier/order.order/delivery', data, errorback);
  },
  /**/
  confirm(data, errorback) {
    return request._post('/supplier/order.Operate/confirmCancel', data, errorback);
  },
  /**/
  refundDetail(data, errorback) {
    return request._get('/supplier/order.refund/detail', data, errorback);
  },
  /**/
  Approval(data, errorback) {
    return request._get('/supplier/order.refund/audit', data, errorback);
  },
  /**/
  receipt(data, errorback) {
    return request._post('/supplier/order.refund/receipt', data, errorback);
  },
  /**/
  extract(data, errorback) {
    return request._post('/supplier/order.operate/extract', data, errorback);
  },
  /**/
  updatePrice(data, errorback) {
    return request._post('/supplier/order.order/updatePrice', data, errorback);
  },
  /**/
  agentOrder(data, errorback) {
    return request._post('/supplier/order.agent/index', data, errorback);
  },
  queryLogistics(data, errorback) {
    return request._post('/supplier/order.order/express', data, errorback);
  },
  updateAddress(data, errorback) {
    return request._post('/supplier/order.order/updateAddress', data, errorback);
  },
  /**/
  batchDelivery(formData, errorback) {
    return request._upload('/supplier/order.operate/batchDelivery', formData, errorback);
  },
  /**/
  orderCancel(formData, errorback) {
    return request._upload('/supplier/order.order/orderCancel', formData, errorback);
  },
  /**/
  virtual(formData, errorback) {
    return request._upload('/supplier/order.order/virtual', formData, errorback);
  },
}

export default OrderApi;



