<template>
  <!--
       luoyiming
      2020-06-09
      --
  -->
  <el-dialog title="" :visible.sync="dialogVisible" @close='dialogFormVisible' :close-on-click-modal="false"
    :close-on-press-escape="false" width="600px">
    <el-form size="small" :model="form" ref="form">
      <el-form-item label="" :label-width="formLabelWidth" prop="active_name" :rules="[{required: true,message: ' '}]">
        <el-input v-model="form.active_name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <div>
          <el-radio v-model="form.full_type" :label="1"></el-radio>
          <el-radio v-model="form.full_type" :label="2"></el-radio>
        </div>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth" prop="full_value" :rules="[{required: true,message: ' '}]">
        <el-input type="number" :precision="1" :step="1" :min="0" placeholder="" v-model="form.full_value"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <div>
          <el-radio v-model="form.reduce_type" :label="1"></el-radio>
          <el-radio v-model="form.reduce_type" :label="2"></el-radio>
        </div>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth" prop="reduce_value" :rules="[{required: true,message: ' '}]">
        <el-input type="number" :precision="1" :step="1" :min="0" placeholder="" v-model="form.reduce_value"></el-input>
        <div class="tips">
          3030770%
        </div>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogFormVisible"> </el-button>
      <el-button type="primary" @click="edit" :disabled="submit_loading"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import FullreduceApi from '@/api/fullreduce.js';
  export default {
    data() {
      return {
        /**/
        formLabelWidth: '120px',
        /**/
        dialogVisible: false,
        /**/
        submit_loading: false,
      };
    },
    props: ['open_edit', 'form'],
    created() {
      this.dialogVisible = this.open_edit;
    },
    methods: {
      /**/
      edit() {
        let self = this;
        let params = this.form;
        self.$refs.form.validate((valid) => {
          if (valid) {
            self.submit_loading = true;
            FullreduceApi.edit(params, true)
              .then(data => {
                self.submit_loading = false;
                self.$message({
                  message: '',
                  type: 'success'
                });
                self.dialogFormVisible(true);

              })
              .catch(error => {
                self.submit_loading = false;
              });

          }
        });
      },

      /**/
      dialogFormVisible(e) {
        if (e) {
          this.$emit('closeDialog', {
            type: 'success',
            openDialog: false
          })
        } else {
          this.$emit('closeDialog', {
            type: 'error',
            openDialog: false
          })
        }
      }

    }
  };
</script>

<style></style>



