<template>
  <!--
      	luoyiming
      	2020-06-04
      	-
      -->
  <div class="common-seach-wrap">
    <!---->
    <List v-if="activeName=='list'"></List>
    <!---->
    <Receive v-if="activeName=='receive'"></Receive>
    <!---->
    <SendCoupon v-if="activeName=='send'"></SendCoupon>
  </div>
</template>
<script>
  import bus from '@/utils/eventBus.js';
  import List from './coupon/index';
  import Receive from './coupon/Receive';
  import SendCoupon from './coupon/SendCoupon';
  export default {
    components: {
      List,
      Receive,
      SendCoupon
    },
    data() {
      return {
        /**/
        loading: true,
        form: {},
        /**/
        param: {},
        /**/
        activeName: 'list',
        /**/
        sourceList: [
            {
                key: 'list',
                value: '',
                path:'/coupon/coupon/index'
            },
            {
                key: 'receive',
                value: '',
                path:'/coupon/coupon/receive'
            },
            {
                key: 'send',
                value: '',
                path:'/coupon/coupon/SendCoupon'
            }
        ],
        /**/
        tabList:[],
      };
    },
    mounted() {
      this.init();
    },
    beforeDestroy () {
        //
        bus.$emit('tabData', {active: null,tab_type:'coupon', list: []});
        bus.$off('activeValue');
    },
    methods: {
      init(){
        this.tabList = this.authFilter();

        if(this.tabList.length>0){
          this.activeName=this.tabList[0].key;
        }

        if (this.$route.query.type != null) {
          this.activeName = this.$route.query.type;
        }

        /**/
        bus.$on('activeValue', res =>
        {
            this.activeName = res;
        });

        //
        let params = {
            active: this.activeName,
            list: this.tabList,
            tab_type:'coupon'
        }
        bus.$emit('tabData', params);
      },
      /**/
      authFilter(){
        let list=[];
        for(let i=0;i<this.sourceList.length;i++){
          let item=this.sourceList[i];
          if(this.$filter.isAuth(item.path)){
            list.push(item);
          }
        }
        return list;
      },

    }
  };
</script>

<style>
  .operation-wrap {
    height: 124px;
    border-radius: 8px;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    padding: 30px 30px;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    overflow: hidden;
    background: #909399;
    background-size: 100% 100%;
    color: #fff;
  }
</style>



