import request from '@/utils/request'

let ShopApi = {

  /**/
  security(data, errorback) {
    return request._post('/supplier/shop.security/index', data, errorback);
  },
  /**/
  apply(data, errorback) {
    return request._post('/supplier/shop.security/apply', data, errorback);
  },
  /**/
  quit(data, errorback) {
    return request._post('/supplier/shop.security/quit', data, errorback);
  },
}

export default ShopApi;



