<template>
  <!--
    luoyiming
    2020-06-04
    --
  -->
  <div class="user" v-loading="loading">
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="" name="has_audit"></el-tab-pane>
      <el-tab-pane label="" name="wait_audit"></el-tab-pane>
    </el-tabs>
    <div class="common-level-rail">
      <el-button size="small" type="primary" icon="el-icon-plus" @click="openProduct()"></el-button>
    </div>
    <div class="product-content point-list">
      <div class="table-wrap">
        <el-table size="small" :data="tableData" border style="width: 100%">
          <el-table-column prop="product_name" label="" width="360">
            <template slot-scope="scope">
              <div class="product-info">
                <div class="pic" style="width: 40px; height: 40px;"><img v-img-url="scope.row.product.image[0].file_path"
                    alt="" /></div>
                <div class="info">
                  <div class="name">{{ scope.row.product.product_name }}</div>
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <span v-if="scope.row.product.spec_type == 10"></span>
              <span v-if="scope.row.product.spec_type == 20"></span>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              {{ scope.row.sku[0].point_num }}
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <span class="orange fb">{{ scope.row.sku[0].point_money }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="limit_num" label=""></el-table-column>
          <el-table-column prop="stock" label=""></el-table-column>
          <el-table-column prop="sort" label=""></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <span v-if="scope.row.status == 0" class="green"></span>
              <span v-if="scope.row.status == 10" class="green"></span>
              <span v-if="scope.row.status == 20" class="gray"></span>
            </template>
          </el-table-column>
          <el-table-column prop="remark" label=""></el-table-column>
          <el-table-column fixed="right" label="" width="90">
            <template slot-scope="scope">
              <el-button v-if="scope.row.status != 10" @click="edit(scope.row)" type="text" size="small"></el-button>
              <el-button @click="delClick(scope.row)" type="text" size="small"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!---->
      <div class="pagination">
        <el-pagination background :current-page="curPage" :page-size="pageSize" layout="total, prev, pager, next, jumper"
          :total="totalDataNumber"></el-pagination>
      </div>
    </div>

    <!---->
    <Product :isproduct="isproduct" :excludeIds="exclude_ids" :islist="false" @closeDialog="closeProductFunc"></Product>
  </div>
</template>
<script>
  import PointsApi from '@/api/points';
  import Product from '@/components/product/Product.vue';
  export default {
    components: {
      /**/
      Product
    },
    data() {
      return {
        formInline: {},
        form: {},
        tableData: [],
        /**/
        pageSize: 20,
        /**/
        totalDataNumber: 0,
        /**/
        curPage: 1,
        /**/
        loading: true,
        /**/
        isproduct: false,
        /*id*/
        exclude_ids: [],
        /*id*/
        activeName: 'has_audit'
      };
    },
    created() {
      /**/
      this.getData();
    },
    methods: {
      /**/
      getData() {
        let self = this;
        let Params = {};
        Params.page = self.curPage;
        Params.list_rows = self.pageSize;
        Params.status = self.activeName;
        PointsApi.myList(Params, true)
          .then(data => {
            self.loading = false;
            self.tableData = data.data.list.data;
            self.totalDataNumber = data.data.list.total;
            self.exclude_ids = data.data.exclude_ids;
          })
          .catch(error => {});
      },
      /**/
      handleCurrentChange(val) {
        let self = this;
        self.curPage = val;
        self.loading = true;
        self.getData();
      },
      /**/
      openProduct() {
        this.isproduct = true;
      },
      /**/
      handleClick(tab, event) {
        let self = this;
        self.curPage = 1;
        self.getData();
      },
      /**/
      closeProductFunc(e) {
        this.isproduct = false;
        if (e.type == 'success') {
          this.$router.push('/activity/point/add?product_id=' + e.params.product_id);
        }
      },
      /**/
      handleSizeChange(val) {
        this.curPage = 1;
        this.pageSize = val;
        this.getTableList();
      },
      edit(row) {
        this.$router.push('/activity/point/edit?point_product_id=' + row.point_product_id);
      },
      /**/
      delClick: function(row) {
        let self = this;
        self
          .$confirm('?', '', {
            type: 'warning'
          })
          .then(() => {
            PointsApi.delProduct({
              point_product_id: row.point_product_id
            }).then(data => {
              self.$message({
                message: '',
                type: 'success'
              });
              self.getData();
            });
          });
      },
    }
  };
</script>

<style>
</style>



