<template>
  <!--
      	luoyiming
      	2019-06-04
      	--
      -->
  <div class="user">
    <!---->
    <div class="common-seach-wrap">
    </div>

    <div class="product-content">
      <el-form ref="form" :model="form" label-width="100px">
        <div class="table-wrap">
          <el-table size="small" :data="tableData" border style="width: 100%" v-loading="loading">
            <el-table-column prop="user.nickName" label=""></el-table-column>
            <el-table-column prop="coupon_id" label="ID"></el-table-column>
            <el-table-column prop="name" label=""></el-table-column>
            <el-table-column prop="coupon_type" label=""></el-table-column>
            <el-table-column prop="min_price" label=""></el-table-column>
            <el-table-column prop="seckill_stock" label="">
              <template slot-scope="scope">
                <div v-if="scope.row.coupon_type.value==10">
                  <span> <strong class="orange">{{scope.row.reduce_price}}</strong> </span>
                </div>
                <div v-if="scope.row.coupon_type.value==20">
                  <span> <strong class="orange">{{scope.row.discount}}</strong> </span>
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="seckill_stock" label="">
              <template slot-scope="scope">
                <div v-if="scope.row.expire_type==10">
                  <span> <strong class="orange">{{scope.row.expire_day}}</strong> </span>
                </div>
                <div v-if="scope.row.expire_type==20">
                  <span>
                    {{scope.row.start_time.text}}
                    ~
                    {{scope.row.end_time.text}}
                  </span>
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="create_time" label=""></el-table-column>
          </el-table>
        </div>
      </el-form>
      <!---->
      <div class="pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background :current-page="curPage"
           :page-size="pageSize" layout="total, prev, pager, next, jumper"
          :total="totalDataNumber">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
  import CouponApi from '@/api/coupon.js';
  export default {
    data() {
      return {
        formInline: {},
        form: {},
        tableData: [],
        /**/
        pageSize: 20,
        /**/
        totalDataNumber: 0,
        /**/
        curPage: 1,
        /**/
        loading: true,
      };
    },
    created() {
      /**/
      this.getData();
    },
    methods: {
      /**/
      getData() {
        let self = this;
        let Params = {};
        Params.page = self.curPage;
        Params.list_rows = self.pageSize;
        CouponApi.receiveList(Params, true)
          .then(data => {
            self.loading = false;
            self.tableData = data.data.list.data;
            self.totalDataNumber = data.data.list.total
          })
          .catch(error => {});
      },
      /**/
      handleCurrentChange(val) {
        let self = this;
        self.curPage = val;
        self.loading = true;
        self.getData();
      },

      /**/
      handleSizeChange(val) {
        this.curPage = 1;
        this.pageSize = val;
        this.getTableList();
      }
    }
  };
</script>

<style>
</style>



