<template>
  <!--
    	luoyiming
    	2019-10-25
    	-
    -->
  <el-dialog title="" :visible.sync="dialogVisible" @close="cancelFunc" :close-on-click-modal="false" :close-on-press-escape="false" width="800px" :append-to-body='true'>
    <div class="common-seach-wrap">
      <el-form size="small" :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label=""><el-input placeholder="/" v-model="formInline.search"></el-input></el-form-item>
        <el-form-item>
          <el-button icon="el-icon-search" @click="search"></el-button>
        </el-form-item>
      </el-form>
    </div>

    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table :data="tableData" size="small" border style="width: 100%" v-loading="loading" @selection-change="handleSelectionChange">
          <el-table-column prop="user.user_id" label="ID" width="70"></el-table-column>
          <el-table-column prop="" label="" width="70">
            <template slot-scope="scope">
              <img :src="scope.row.user.avatarUrl" class="radius" width="30" height="30" />
            </template>
          </el-table-column>
          <el-table-column prop="user.nickName" label=""></el-table-column>
          <el-table-column prop="user.mobile" label=""></el-table-column>
          <el-table-column prop="user.create_time" label=""></el-table-column>
          <!-- <el-table-column label="" width="80">
            <template slot-scope="scope">
              <el-button type="primary" size="mini" @click="selectUser(scope.row)"></el-button>
            </template>
          </el-table-column> -->
          <el-table-column type="selection" width="45"></el-table-column>
        </el-table>
      </div>

      <!---->
      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          background
          :current-page="curPage"
          :page-sizes="[2, 10, 20, 50, 100]"
          :page-size="pageSize"
          layout="total, prev, pager, next, jumper"
          :total="totalDataNumber"
        ></el-pagination>
      </div>
    </div>

    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="dialogVisible = false"> </el-button>
      <el-button size="small" type="primary" @click="confirmFunc"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
import DataApi from '@/api/data.js';
export default {
  data() {
    return {
      /**/
      loading: true,
      /**/
      curPage: 1,
      /**/
      pageSize: 15,
      /**/
      totalDataNumber: 0,
      /**/
      formInline: {
        /**/
        search: '',
      },
      /**/
      tableData: [],
      /**/
      multipleSelection: [],
      /**/
      dialogVisible: false
    };
  },
  props: {
    is_open: Boolean
  },
  watch: {
    is_open: function(n, o) {
      if (n != o) {
        this.dialogVisible = n;
        if (n) {
          this.getTableList();
        }
      }
    }
  },
  created() {},
  methods: {
    /**/
    handleCurrentChange(val) {
      this.curPage = val;
      this.getTableList();
    },

    /**/
    handleSizeChange(val) {
      this.curPage = 1;
      this.pageSize = val;
      this.getTableList();
    },

    /**/
    getTableList() {
      let self = this;
      self.loading = true;
      let params = self.formInline;
      params.page = self.curPage;
      params.list_rows = self.pageSize;
      DataApi.getUser(params,true).then(data => {
          self.loading = false;
          self.tableData = data.data.list.data;
          self.totalDataNumber = data.data.list.total;
          self.gradeList = data.data.grade;
        })
        .catch(error => {
          self.loading = false;
        });
    },

    /**/
    search() {
      this.curPage = 1;
      this.tableData = [];
      this.getTableList();
    },

    /**/
    confirmFunc() {
      let params = this.multipleSelection;
      this.emitFunc(params);
    },

    /**/
    cancelFunc(e) {
      this.emitFunc();
    },

    /**/
    emitFunc(e) {
      this.dialogVisible = false;
      if (e && typeof e != 'undefined') {
        this.$emit('close', {
          type: 'success',
          params: e
        });
      } else {
        this.$emit('close', {
          type: 'error'
        });
      }
    },

    /**/
    selectUser(val) {
      this.multipleSelection = val;
      this.confirmFunc();
    },

    /**/
    handleSelectionChange(e) {
      this.multipleSelection = e;
    }
  }
};
</script>

<style></style>



