<template>
  <!--
    luoyiming
    2020-06-04
    -
  -->
  <div>
    <!---->
    <Room v-if="activeName == 'room'"></Room>
    <Order v-if="activeName == 'order'"></Order>

  </div>
</template>
<script>
import bus from '@/utils/eventBus.js';
import Room from './room/room.vue';
import Order from './order/list.vue';

export default {
  components: {
    Room,
    Order
  },
  data() {
    return {
      /**/
      param: {},
      /**/
      activeName: 'room',
      /**/
      sourceList: [
        {
          key: 'room',
          value: '',
        },
        {
          key: 'order',
          value: '',
        }
      ],
      /**/
      tabList:[],
    };
  },
  created() {

    this.tabList=this.sourceList;//this.authFilter();

    if(this.tabList.length>0){
      this.activeName=this.tabList[0].key;
    }

    if (this.$route.query.type != null) {
      this.activeName = this.$route.query.type;
    }

    /**/
    bus.$on('activeValue', res => {
      this.activeName = res;
    });

    //
    let params = {
      active: this.activeName,
      list: this.tabList,
      tab_type: 'live'
    };
    bus.$emit('tabData', params);
  },
  beforeDestroy() {
    //
    bus.$emit('tabData', { active: null, tab_type: 'live', list: [] });
    bus.$off('activeValue');
  },
  methods: {

    /**/
    authFilter(){
      let list=[];
      for(let i=0;i<this.sourceList.length;i++){
        let item=this.sourceList[i];
        if(this.$filter.isAuth(item.path)){
          list.push(item);
        }
      }
      return list;
    },

  }
};
</script>



