<template>
  <!--
      	luoyiming
      	2019-06-04
      	--
      -->
  <div class="user">
    <div class="common-form"></div>
    <div class="product-content">
      <el-form ref="form" size="small" :model="form" label-width="150px">
        <el-form-item label="">
          <el-radio v-model="form.send_type" label="1"></el-radio>
          <el-radio v-model="form.send_type" label="2"></el-radio>
          <el-radio v-model="form.send_type" label="3"></el-radio>
        </el-form-item>

        <el-form-item label="" v-if="form.send_type == 2">
          <el-select v-model="form.user_level" placeholder="">
            <el-option v-for="(item, index) in grade" :key="index" :label="item.name" :value="item.grade_id"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" v-if="form.send_type == 3">
          <div class="d-s-s d-c">
            <el-button @click="openGetuser" icon="el-icon-plus"></el-button>
            <ul class="d-s-s select-user">
              <li class="mr10 d-c-c d-c p-10-0" v-for="(item,index) in select_list" :key="index">
                  <img :src="item.avatarUrl" class="radius" width="30" height="30" />
                  <p class="lh18 ww100 text-ellipsis">{{item.nickName}}</p>
              </li>
            </ul>
          </div>
        </el-form-item>

        <el-form-item label=" " prop="name" :rules="[{ required: true, message: ' ' }]">
          <el-input type="text" v-model="form.name" placeholder="" class="max-w460" :disabled="true"></el-input>
          <el-button @click="openConpon" icon="el-icon-plus"></el-button>
          <p> </p>
        </el-form-item>

        <!---->
        <div class="common-button-wrapper">
          <el-button size="small" type="primary" @click="onSubmit" :loading="loading"></el-button>
        </div>
      </el-form>
    </div>

    <!---->
    <GetUser :is_open="open_getuser" @close="closeGetuserFunc"></GetUser>

    <!---->
    <GetCoupon v-if="open_coupon" :open_add="open_coupon" @closeDialog="closeCouponFunc($event, 'add')"></GetCoupon>
  </div>
</template>

<script>
import CouponApi from '@/api/coupon.js';
import GetUser from '@/components/user/GetUser.vue';
import GetCoupon from '@/components/coupon/GetCoupon.vue';
export default {
  components: {
    GetUser,
    GetCoupon
  },
  data() {
    return {
      /**/
      loading: false,
      /**/
      form: {
        send_type: '1',
        coupon_id: 0,
        user_level: '',
        user_ids: '',
        name: ''
      },
      /**/
      grade: {},
      /**/
      open_getuser: false,
      /**/
      select_list:[],
      /**/
      open_coupon: false
    };
  },
  created() {
    /**/
    this.getGradeList();
  },
  methods: {
    /**/
    getGradeList() {
      let self = this;
      CouponApi.toSendCoupon({}, true)
        .then(data => {
          self.grade = data.data.list;
        })
        .catch(error => {});
    },

    /**/
    openGetuser() {
      this.open_getuser = true;
    },

    /**/
    closeGetuserFunc(e) {
      if (e.type != 'error') {
        this.select_list=this.select_list.concat(e.params);
      }
      this.open_getuser = false;
    },

    /**/
    openConpon() {
      this.open_coupon = true;
    },

    /**/
    closeCouponFunc(e) {
      this.open_coupon = false;
      if (e.type != 'error') {
        this.form.name = e.params.name;
        this.form.coupon_id = e.params.coupon_id;
      }
    },

    /**/
    onSubmit() {
      let self = this;
      let params = self.form;
      /**/
      if(params.send_type == 3){
        let user_ids=[];
        self.select_list.forEach((item)=>{
          user_ids.push(item.user_id);
        })
        params.user_ids=user_ids.join(',');
      }
      CouponApi.SendCoupon(params, true)
        .then(data => {
          self.loading = false;
          self.$message({
            message: '',
            type: 'success'
          });
        })
        .catch(error => {
          self.loading = false;
        });
    }
  }
};
</script>

<style scoped="scoped">
  .select-user li{ width: 30px;}
</style>



