import request from '@/utils/request'

let LiveApi = {

    /**/
    getList(data, errorback) {
        return request._post('/supplier/plus.live.wx/index', data, errorback);
    },

      /**/
    syn(data, errorback) {
        return request._post('/supplier/plus.live.wx/syn', data, errorback);
    },

    /**/
    settop(data, errorback) {
        return request._post('/supplier/plus.live.wx/settop', data, errorback);
    },


    /**/
    getRoom(data, errorback) {
        return request._post('/supplier/live.room/index', data, errorback);
    },

    /**/
    editRoom(data, errorback) {
        return request._post('/supplier/live.room/edit', data, errorback);
    },

    /**/
    deleteRoom(data, errorback) {
        return request._post('/supplier/live.room/delete', data, errorback);
    },

    /**/
    getGift(data, errorback) {
        return request._post('/supplier/live.gift/index', data, errorback);
    },

    /**/
    addGift(data, errorback) {
        return request._post('/supplier/live.gift/add', data, errorback);
    },

    /**/
    editGift(data, errorback) {
        return request._post('/supplier/live.gift/edit', data, errorback);
    },

    /**/
    deleteGift(data, errorback) {
        return request._post('/supplier/live.gift/delete', data, errorback);
    },


    /**/
    getProduct(data, errorback) {
        return request._post('/supplier/live.room/product', data, errorback);
    },
    /**/
    getOrderList(data, errorback) {
        return request._post('/supplier/live.room/getOrderList', data, errorback);
    },
    /**/
    getUserGift(data, errorback) {
        return request._post('/supplier/live.room/user_gift', data, errorback);
    },

}

export default LiveApi;



