<template>
  <!--
       luoyiming
      2019-10-26
      -
  -->
  <div class="product-add">
    <!--form-->
    <el-form size="small" ref="form" :model="form" label-width="200px">
      <!---->
      <div class="common-form"></div>

      <el-form-item label="">
        <div>
          <el-radio v-model="form.is_open" :label="1"></el-radio>
          <el-radio v-model="form.is_open" :label="0"></el-radio>
        </div>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="form.printer_id" placeholder="">
          <el-option v-for="(item,index) in printerList" :key="index" :label="item.printer_name" :value="item.printer_id+''"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">

        <!-- <el-checkbox-group v-model="checked" @change="handleCheckedCitiesChange">
          <el-checkbox v-for="(item,index) in all_type"  :label="index" :key="index"></el-checkbox>
        </el-checkbox-group> -->
        <template>
          <!-- `checked`  true  false -->
          <el-checkbox v-model="checked" @change="handleCheckedCitiesChange"></el-checkbox>
        </template>
      </el-form-item>


      <!---->
      <div class="common-button-wrapper">
        <el-button type="primary" @click="onSubmit" :loading="loading"></el-button>
      </div>



    </el-form>


  </div>

</template>

<script>
  import SettingApi from '@/api/setting.js';

  export default {
    data() {
      return {
        /**/
        // activeIndex: '1',
        /*form*/
        form: {
          is_open: '',
          printer_id: '',
          order_status: [],
        },
        checked: false,
        printerList: [],
        loading: false,


      };
    },
    created() {
      this.getData()
    },

    methods: {
      getData() {
        let self = this;
        SettingApi.printingDetail({}, true)
          .then(data => {
            let vars = data.data.vars.values;
            self.form.is_open = vars.is_open;
            self.form.printer_id = '' + vars.printer_id;
            self.form.order_status = vars.order_status;
            self.printerList = data.data.vars.printerList;
            if (vars.order_status != null && vars.order_status[0] == 20) {
              self.checked = true;
            }

          })
          .catch(error => {});
      },
      //
      onSubmit() {
        let self = this;
        let params = this.form;
        self.loading = true;
        SettingApi.editPrinting(params, true)
          .then(data => {
            self.loading = false;
            self.$message({
              message: '',
              type: 'success'
            });
            // self.$router.push('/setting/Printing');

          })
          .catch(error => {
            self.loading = false;
          });
      },
      //
      handleCheckedCitiesChange(e) {
        let self = this;
        if (e) {
          self.form.order_status[0] = 20;
        } else {
          self.form.order_status = [];
        }
      },

    }

  };
</script>

<style>
  .tips {
    color: #ccc;
  }
</style>



