<template>
  <!--
        luoyiming
        2019-10-25
        --
    -->
  <el-dialog title="" :visible.sync="dialogVisible" @close="dialogFormVisible" :close-on-click-modal="false" :close-on-press-escape="false" :append-to-body='true'>
    <!--form-->
    <el-form size="small" ref="form" :model="form" :rules="formRules" :label-width="formLabelWidth">
      <el-form-item label="" prop="user_name"><el-input v-model="form.user_name" placeholder=""></el-input></el-form-item>
      <el-form-item label="" prop="access_id">
        <el-select v-model="form.access_id" :multiple="true">
          <el-option v-for="item in roleList" :value="item.role_id" :key="item.role_id" :label="item.role_name_h1"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-row>
          <el-button @click="addClick" icon="el-icon-plus"></el-button>
          <div v-if="form.user_info.user_id!=''" class="img">
            <img :src="form.user_info.user.avatarUrl" width="100" height="100" />
          </div>
        </el-row>
      </el-form-item>
      <el-form-item label="" prop="password"><el-input v-model="form.password" placeholder="" type="password"></el-input></el-form-item>
      <el-form-item label="" prop="confirm_password"><el-input v-model="form.confirm_password" placeholder="" type="password"></el-input></el-form-item>
      <el-form-item label="" prop="real_name"><el-input v-model="form.real_name"></el-input></el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false"> </el-button>
      <el-button type="primary" @click="onSubmit" :loading="loading"> </el-button>
    </div>
    <!---->
    <GetUser :is_open="open_add" @close="closeDialogFunc"></GetUser>
  </el-dialog>
</template>

<script>
import AuthApi from '@/api/auth.js';
import GetUser from '@/components/user/GetUser.vue';
export default {
  data() {
    return {
      /**/
      formLabelWidth: '120px',
      /**/
      loading: false,
      /**/
      open_add: false,
      /**/
      dialogVisible: false,
      /*form*/
      form: {
        user_name: '',
        access_id: [],
        user_id: 0,
        user_info:{
          user:{

          }
        }
      },
      /*form*/
      formRules: {
        user_name: [
          {
            required: true,
            message: ' ',
            trigger: 'blur'
          }
        ],
        role_id: [
          {
            required: true,
            message: ' ',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: ' ',
            trigger: 'blur'
          }
        ],
        confirm_password: [
          {
            required: true,
            message: ' ',
            trigger: 'blur'
          }
        ],
        real_name: [
          {
            required: true,
            message: ' ',
            trigger: 'blur'
          }
        ]
      }
    };
  },
  props: ['open', 'roleList'],
  watch: {
    open: function(n, o) {
      if (n != o) {
        this.dialogVisible = this.open;
      }
    }
  },
  components:{
    GetUser
  },
  watch: {
    open: function(n, o) {
      if (n != o) {
        this.dialogVisible = this.open;
        this.form={
        user_name: '',
        access_id: [],
        user_id: 0,
        user_info:{
          user:{

          }
        }
      }
      }
    }
  },
  methods: {
    /**/
    onSubmit() {
      let self = this;
      self.loading = true;
      let params = self.form;
      AuthApi.userAdd(params, true)
        .then(data => {
          self.loading = false;
          self.$message({
            message: '',
            type: 'success'
          });

          self.dialogFormVisible(true);
        })
        .catch(error => {
          self.loading = false;
        });
    },
      addClick() {
        this.open_add = true;
      },
      /**/
      closeDialogFunc(e) {
        if (e.type != 'error') {
          this.form.user_info = e.params[0];
          this.form.user_id = e.params[0].user_id;
        }
        this.open_add = false;
      },
    /**/
    dialogFormVisible(e) {
      if (e) {
        this.$emit('close', {
          type: 'success',
          openDialog: false,
        });
      } else {
        this.$emit('close', {
          type: 'error',
          openDialog: false
        });
      }
    }
  }
};
</script>

<style></style>



