<template>
  <div class="product-add" v-loading="loading">
    <el-tabs v-model="activeName" type="card">
      <el-tab-pane label="" name="basic"></el-tab-pane>
      <el-tab-pane label="" name="spec"></el-tab-pane>
      <el-tab-pane label="" name="content"></el-tab-pane>
      <el-tab-pane label="" name="fenqi"></el-tab-pane>
      <el-tab-pane label="" name="buyset"></el-tab-pane>
    </el-tabs>
    <!--form-->
    <el-form size="small" ref="form" :model="form" label-width="180px" v-if="!loading">
      <!---->
      <Basic v-show="activeName == 'basic'"></Basic>
      <!---->
      <Spec v-show="activeName == 'spec'"></Spec>
      <!---->
      <Content v-show="activeName == 'content'"></Content>
      <!--  -->
      <fenqi v-show="activeName == 'fenqi'"></fenqi>
      <!---->
      <Buyset v-show="activeName == 'buyset'"></Buyset>
      <!---->
      <div class="common-button-wrapper">
        <el-button size="small" type="info" @click="cancelFunc"></el-button>
        <el-button size="small" type="primary" @click="onSubmit" :disabled="save_loading"></el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
  import PorductApi from '@/api/product.js';
  import Basic from './part/Basic.vue';
  import Spec from './part/Spec.vue';
  import Content from './part/Content.vue';
  import Buyset from './part/Buyset.vue';
  import fenqi from './part/fenqi.vue';

  import {
    formatModel
  } from '@/utils/base.js';
  export default {
    components: {
      /**/
      Basic,
      /**/
      Spec,
      /**/
      Content,
      /**/
      Buyset,
      // 
      fenqi
    },
    data() {
      return {
        activeName: 'basic',
        /*ID*/
        product_id: 0,
        /**/
        scene: 'edit',
        /**/
        loading: true,
        /**/
        save_loading: false,
        /*form*/
        form: {
          model: {},
          /**/
          catgory: [],
          /**/
          delivery: [],
          /**/
          gradeList: [],
          /**/
          specData: null,
          /**/
          isSpecLocked: false,
          /**/
          basicSetting: {},
          /**/
          agentSetting: {},
          audit_setting: {}
        },
        /**/
        model: {
          /**/
          product_name: '',
          /**/
          category_id: null,
          /**/
          image: [],
          is_picture: 0,
          contentImage: [],
          video_id: 0,
          poster_id: 0,
          /**/
          selling_point: '',
          /*,1020*/
          spec_type: 10,
          /*,2010*/
          deduct_stock_type: 20,
          /**/
          sku: {
            /**/
            product_price: 0,
            /**/
            line_price: 0,
            /**/
            stock_num: 0,
            /**/
             product_weight: 0,
             // 
             buy_out_price: 0,
            //  
            deposit:0,
            /**/
            product_no: '',
            
          },
          /**/
          spec_many: {
            /**/
            spec_attr: [],
            /**/
            spec_list: []
          },
          /**/
          content: '',
          /*ID*/
          delivery_id: '',
          /**/
          product_status: 10,
          audit_status: 0,
          /*100*/
          product_sort: 100,
          /*,10*/
          is_points_gift: 1,
          /*,10*/
          is_points_discount: 1,
          /**/
          max_points_discount: 0,
          /**/
          is_agent: 0,
          /*,01*/
          is_ind_agent: 0,
          /*,1020*/
          agent_money_type: 10,
          /**/
          first_money: null,
          /**/
          second_money: null,
          /**/
          third_money: null,
          /**/
          is_virtual: 0,
          /**/
          limit_num: 0,
          /**/
          virtual_auto: 0,
          /**/
          virtual_content: '',
          tableData:[{
            lease_time:'1',
            val:'1',
            value:'1'
          }]
        },
      };
    },
    provide: function() {
      return {
        form: this.form
      };
    },
    created() {
      /**/
      this.product_id = this.$route.query.product_id;
      this.scene = this.$route.query.scene;
      this.getData();
    },
    methods: {
      /**
       * 
       */
      getData: function() {
        let self = this;
        PorductApi.getEditData({
              product_id: self.product_id,
              scene: self.scene
            },
            true
          )
          .then(res => {
            console.log(res,"kkkk")
            self.loading = false;
            self.form.scene = self.scene;
            Object.assign(self.form, res.data);
            if (res.data.specData) {
              self.$set(self.form.model, 'spec_many', res.data.specData);
            } else {
              self.$set(self.form.model, 'spec_many', {
                spec_attr: [],
                spec_list: []
              });
              self.form.model.sku = res.data.model.sku[0];
            }
            self.form.model.product_status = res.data.model.product_status.value;
            if(self.form.model.delivery_id == 0){
              self.$set(self.form.model, 'is_delivery_free', 0);
            }else{
              self.$set(self.form.model, 'is_delivery_free', 1);
            }
            this.model.tableData = res.data.model.tableData
            //
            if(self.form.audit_setting.edit_audit == 1 && self.form.scene != 'add'){
              self.form.model.audit_status = 0;
            }
          })
          .catch(error => {
            self.loading = false;
          });
      },

      /*JSON*/
      convertJson(list) {
        let obj = {};
        list.forEach(item => {
          obj[item.grade_id] = item.product_equity;
        });
        return JSON.stringify(obj);
      },

      /**/
      onSubmit: function() {
        let self = this;
        self.$refs.form.validate(valid => {
          if (valid) {
            let params = formatModel(self.model, self.form.model);
            params.scene = self.scene;
            params.image = self.ImgKeepId(params.image);
            params.product_id = self.product_id;
            self.save_loading = true;
            PorductApi.editProduct({
                product_id: self.product_id,
                scene: self.scene,
                params: JSON.stringify(params)
              }, true)
              .then(data => {
                self.save_loading = false;
                self.$message({
                  message: '',
                  type: 'success'
                });
                self.cancelFunc();
              })
              .catch(error => {
                self.save_loading = false;
              });
          }else{
            self.$message({
              message: '',
              type: 'error'
            });
          }
        });
      },

      /*id*/
      ImgKeepId(list) {
        let arr = [];
        for (let i = 0, length = list.length; i < length; i++) {
          let obj = {
            image_id: list[i].image_id,
            file_id: list[i].file_id
          };
          arr.push(obj);
        }
        return arr;
      },

      /**/
      cancelFunc() {
        this.$router.back(-1);
      }
    }
  };
</script>

<style lang="scss" scoped>
  .basic-setting-content {}

  .product-add {
    padding-bottom: 50px;
  }
</style>



