<template>
  <!--
          luoyiming
          2019-10-24
          --
      -->
  <div class="pie-container d-b-s d-c">
    <div class="ww100 d-b-c lh30">
      <span class="f16"></span>
      <el-select size="mini" v-model="selectDay" @change="changeFunc" placeholder="">
        <el-option v-for="item in days" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>
    </div>
    <div class="ww100">
      <div class="Echarts"><div id="PieBox" class="ww100"></div></div>
    </div>
    <div></div>
  </div>
</template>

<script>
import StatisticsApi from '@/api/statistics.js';
export default {
  data() {
    return {
      /**/
      loading: true,
      /**/
      days: [
        {
          label: '',
          value: 1
        },
        {
          label: '7',
          value: 7
        },
        {
          label: '30',
          value: 30
        },
        {
          label: '90',
          value: 90
        },
        {
          label: '180',
          value: 180
        },
        {
          label: '1',
          value: 365
        },
        {
          label: '2',
          value: 730
        },
        {
          label: '',
          value: 0
        }
      ],
      /**/
      selectDay: 7,
      /**/
      payScale: {},
      /**/
      myChart: null,
      /**/
      option: {
        title: {
          text: '',
          subtext: '',
          left: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        }
      }
    };
  },
  mounted() {
    this.myEcharts();
  },
  methods: {
    /**/
    changeFunc() {
      this.getData();
    },

    /**/
    myEcharts() {
      // domecharts
      this.myChart = this.$echarts.init(document.getElementById('PieBox'));
      this.getData();
    },

    /**/
    createOption() {
      if (!this.loading) {

        this.option.color=["#e2e7f2", "#409EFF"];

        this.option.legend = {
          orient: 'vertical',
          left: 'left',
          data: [
            {
              name: '',
              color:"#666"

            },
            {
              name: '',
               color:"#666"
            }
          ]
        };
        this.option.series = [
          {
            name: '',
            type: 'pie',
            radius: '55%',
            center: ['50%', '60%'],
            data: [{ value: this.payScale.no_pay, name: '' }, { value: this.payScale.pay, name: ''}],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ];

        this.myChart.setOption(this.option);
        this.myChart.resize();
      }
    },

    /**/
    getData() {
      let self = this;
      StatisticsApi.getUserScale(
        {
          day: self.selectDay
        },
        true
      )
        .then(res => {
          self.payScale = res.data.payScale;
          self.loading = false;
          self.createOption();
        })
        .catch(error => {});
    }
  }
};
</script>

<style scoped="scoped">
.pie-container {
  padding: 10px 20px;
  width: 30%;
  box-sizing: border-box;
}
.Echarts > div {
  height: 400px;
}
</style>



