<template>
  <!--
    	luoyiming
    	2019-10-24
    	--
    -->
  <el-dialog title="" :visible.sync="dialogVisible" @close="dialogFormVisible" :close-on-click-modal="false" :close-on-press-escape="false" width="30%">
    <el-form size="small" :model="form" ref="order">
      <el-form-item label=""
      :label-width="formLabelWidth"
      prop="money"
      :rules="[{ required: true, message: '' }]">
        <el-input type="number" v-model="form.money" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label=""
      :label-width="formLabelWidth"
      prop="pay_type"
      :rules="[{ required: true, message: '' }]">
        <el-radio v-model="form.pay_type" :label="10"></el-radio>
        <el-radio v-model="form.pay_type" :label="20"></el-radio>
        <!-- <el-radio v-model="form.pay_type" :label="30"></el-radio> -->
      </el-form-item>
    </el-form>

    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogFormVisible"> </el-button>
      <el-button type="primary" @click="submitFunc()" :loading="loading"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
import cashApi from '@/api/cash.js';
export default {
  data() {
    return {
      order_id: 0,
      loading: false,
      /**/
      formLabelWidth: '100px',
      /**/
      dialogVisible: true,
      /**/
      form: {
        money: '',
        pay_type: 10
      }
    };
  },
  props: [],
  created() {},
  methods: {
    //            

    /**/
    submitFunc(e) {
      let self = this;
      self.$refs.order.validate(valid => {
        if (valid) {
          self.loading = true;
          cashApi
            .apply(self.form, true)
            .then(data => {
              self.loading = false;
              self.$message({
                message: '',
                type: 'success'
              });
              self.dialogFormVisible(true);
            })
            .catch(error => {
              self.loading = false;
            });
        }
      });
    },

    /**/
    dialogFormVisible() {
      this.$emit('close', { openDialog: false });
    }
  }
};
</script>

<style></style>



