<template>
  <div>
    <!--form-->
    <el-form size="small" ref="formData" :model="formData" label-width="150px">
      <div class="common-form"></div>
      <el-form-item label="" :rules="[{ required: true, message: '' }]" prop="alipay_name">
        <el-input v-model="formData.alipay_name" placeholder="" class="max-w460"></el-input>
      </el-form-item>

      <el-form-item label="" :rules="[{ required: true, message: '' }]">
        <el-input v-model="formData.alipay_account" placeholder="" class="max-w460"></el-input>
      </el-form-item>
      <div class="common-form"></div>
      <el-form-item label="" :rules="[{ required: true, message: '' }]">
        <el-input v-model="formData.bank_name" placeholder="" class="max-w460"></el-input>
      </el-form-item>

      <el-form-item label="" :rules="[{ required: true, message: '' }]">
        <el-input v-model="formData.bank_account" placeholder="" class="max-w460"></el-input>
      </el-form-item>

      <el-form-item label="" :rules="[{ required: true, message: '' }]">
        <el-input v-model="formData.bank_card" placeholder="" class="max-w460"></el-input>
      </el-form-item>
      <!-- <div class="common-form"></div>
      <el-form-item label="" :rules="[{ required: true, message: '' }]">
        <el-input v-model="formData.weixin_name" placeholder="" class="max-w460"></el-input>
      </el-form-item>

      <el-form-item label="" :rules="[{ required: true, message: '' }]">
        <el-input v-model="formData.weixin_account" placeholder="" class="max-w460"></el-input>
      </el-form-item> -->
      <!---->
      <div class="common-button-wrapper"><el-button size="small" type="primary" @click="onSubmit" :disabled="loasing_submit"></el-button></div>
    </el-form>

    <!---->
    <Upload v-if="isupload" :isupload="isupload" :config="{ total: 3 }" @returnImgs="returnImgsFunc"></Upload>
  </div>
</template>

<script>
import cashApi from '@/api/cash.js';
import Upload from '@/components/file/Upload';
export default {
  components: {
    Upload
  },
  data() {
    return {
      /**/
      loading: true,
      /**/
      loasing_submit: false,
      /**/
      formData: {
        alipay_name:'',
        alipay_account:'',
        bank_name:'',
        bank_account:'',
        bank_card:'',
        weixin_name:'',
        weixin_account:''
      },
      app_id:10001,
      /**/
      isupload: false,
      /**/
      img_type: null
    };
  },
  created() {
    this.getParams();
  },
  methods: {
    /**/
    getParams() {
      let self = this;
      cashApi
        .getAccount({
          app_id:10001
        }, true)
        .then(res => {
          if (res.code == 1) {
            self.formData = res.data.model;
            self.loading = false;
          } else {
            console.log();
          }
        })
        .catch(error => {});
    },

    /**/
    onSubmit() {
      let self = this;
      let params = this.formData;
      params.app_id = self.app_id;
      self.$refs.formData.validate(valid => {
        if (valid) {
          self.loasing_submit = true;
          cashApi
            .setAccount(params, true)
            .then(data => {
              self.loasing_submit = false;
              self.$message({
                message: '',
                type: 'success'
              });
            })
            .catch(error => {
              self.loasing_submit = false;
            });
        }
      });
    },

    /**/
    chooseImg(e) {
      this.img_type = e;
      this.isupload = true;
    },

    /**/
    returnImgsFunc(e) {
      this.isupload = false;
      if (this.img_type != null) {
        this.formData[this.img_type + '_image'] = e[0].file_path;
        this.formData[this.img_type + '_id'] = e[0].file_id;
        this.img_type = null;
      }
    }
  }
};
</script>

<style></style>



