<template>
  <!--
          luoyiming
          2019-10-24
          
      -->
  <div class="login-bg" :style="'background-image:url(' + bgimg_url + ');'">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-position="left" label-width="0px" class="demo-ruleForm login-container">
      <h3 class="title" style="margin-bottom: 20px;">{{supplier_name}}</h3>
      <!---->
      <el-form-item prop="account"><el-input type="text" v-model="ruleForm.account" auto-complete="off" placeholder=""></el-input></el-form-item>
      <!---->
      <el-form-item prop="checkPass"><el-input type="password" v-model="ruleForm.checkPass" auto-complete="off" placeholder=""></el-input></el-form-item>
      <!---->
      <el-form-item><el-button type="primary" style="width:100%;" @click.native.prevent="SubmitFunc" :loading="logining"></el-button></el-form-item>
    </el-form>
  </div>
</template>

<script>
import IndexApi from '@/api/index.js';
import bgimg from '@/assets/img/login_bg.png';
import UserApi from '@/api/user.js';
import { setCookie, setSessionStorage } from '@/utils/base.js';
export default {
  data() {
    return {
      /**/
      loading:true,
      /**/
      supplier_name: '',
      /**/
      bgimg_url: '',
      /**/
      logining: false,
      /**/
      ruleForm: {
        /**/
        account: '',
        /**/
        checkPass: ''
      },
      /**/
      rules: {
        /**/
        account: [{ required: true, message: '', trigger: 'blur' }],
        /**/
        checkPass: [{ required: true, message: '', trigger: 'blur' }]
      },
      /**/
      baseData: {}
    };
  },
  created() {
    this.getData();
  },
  methods: {
    /**/
    getData() {
      let self = this;
      IndexApi.base(true)
        .then(res => {
          self.loading = false;
          self.baseData = res.data;
          self.supplier_name = res.data.settings.supplier_name;
          if(res.data.settings.supplier_bg_img != ''){
            self.bgimg_url = res.data.settings.supplier_bg_img;
          }else{
            self.bgimg_url = bgimg;
          }
        })
        .catch(error => {
          self.loading = false;
        });
    },

    /**/
    SubmitFunc(ev) {
      var _this = this;
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          this.logining = true;
          var Params = {
            username: this.ruleForm.account,
            password: this.ruleForm.checkPass
          };
          /**/
          UserApi.login(Params, true)
            .then(res => {
              this.logining = false;
              if (res.code == 1) {
                /**/
                setCookie('userinfo_supplier',JSON.stringify(res.data));
                /**/
                setCookie('isLogin_supplier', true);
                /**/
                this.$router.push({ path: '/' });
              } else {
                this.$message({
                  message: '',
                  type: 'error'
                });
              }
            })
            .catch(error => {
              //
              this.logining = false;
            });
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.login-bg {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
}

.login-container {
  // box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.1), 0 1px 0px 0 rgba(0, 0, 0, 0.04);
  box-shadow: 0px 0px 2px 2px  #66666640;
  -webkit-border-radius: 5px;
  border-radius: 5px;
  -moz-border-radius: 5px;
  background-clip: padding-box;
  position: fixed;
  width: 350px;
  left: 50%;
  top: 50%;
  margin-left: -175px;
  margin-top: -175px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  .title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
  .remember {
    margin: 0px 0px 35px 0px;
  }
}
</style>



