<template>
  <!--
        luoyiming
        2020-07-25
        --
    -->
  <div class="list">

    <!---->
    <div class="common-seach-wrap">
      <el-form size="small" :inline="true" :model="searchForm" class="demo-form-inline">
        <el-form-item>
          <el-input size="small" v-model="searchForm.search" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button size="small" type="primary" icon="el-icon-search" @click="searchSubmit"></el-button>
        </el-form-item>
      </el-form>
    </div>

    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table size="small" :data="tableData" border style="width: 100%" v-loading="loading">
          <el-table-column prop="name" label="">
            <template slot-scope="scope">
              <div class="text-ellipsis">
                <span class="red" v-if="scope.row.is_top==1">[]</span>{{scope.row.name}}
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="share_img_id" label="" width="120">
            <template slot-scope="scope">
              <div class="picture-box d-c-c" v-if="scope.row.share_img_id>0">
                <img :src="scope.row.share.file_path" alt="">
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="status_text" label=""></el-table-column>
          <el-table-column prop="status_text" label="">
            <template slot-scope="scope">
              <span class="fb green" v-if="scope.row.live_status == 103 || scope.row.live_status == 101">
                {{scope.row.real_start_time_text}}
              </span>
            </template>
          </el-table-column>
          <el-table-column prop="status_text" label="">
            <template slot-scope="scope">
              <span class="fb green" v-if="scope.row.live_status == 103">
                {{scope.row.real_end_time_text}}
              </span>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <span class="fb red" v-if="scope.row.is_delete==1"></span>
              <span class="fb green" v-if="scope.row.is_delete==0"></span>
            </template>
          </el-table-column>
          <el-table-column prop="anchor_name" label="" width="70">
            <template slot-scope="scope">
              <div class="user-info d-s-c">
                <span class="ml10">{{scope.row.user.nickName}}</span>
              </div>
            </template>
          </el-table-column>
          <el-table-column label=""  width="150">
            <template slot-scope="scope">
              <div>{{scope.row.view_num}}</div>
              <div>{{scope.row.gift_num}}</div>
              <div>{{scope.row.sales_num}}</div>
              <div>{{scope.row.digg_num}}</div>
            </template>
          </el-table-column>
          <el-table-column prop="create_time" label="" width="120"></el-table-column>
          <el-table-column fixed="right" label="" width="120">
            <template slot-scope="scope">
              <el-button @click="editClick(scope.row)" type="text" size="small" v-auth="'/live/room/edit'"></el-button>
              <el-button @click="showGift(scope.row)" type="text" size="small" v-auth="'/live/room/user_gift'"></el-button>
             </template>
          </el-table-column>
        </el-table>
      </div>

      <!---->
      <div class="pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background :current-page="curPage"
          :page-size="pageSize" layout="total, prev, pager, next, jumper" :total="totalDataNumber"></el-pagination>
      </div>
    </div>



    <!---->
    <Products :is_open="open_product" :curModel="curModel" @close="closeProducts"></Products>

    <!---->
    <Edit :open="open_edit" :form="curModel" @close="closeEdit"></Edit>

    <!---->
    <Gift :is_open="open_gift" :curModel="curModel" @close="closeGift"></Gift>
  </div>
</template>

<script>
  import liveApi from '@/api/live.js';
  import Products from './dialog/Products.vue';
  import Edit from './dialog/Edit.vue';
  import Gift from './dialog/Gift.vue';
  export default {
    components: {
      Products,
      Edit,
      Gift
    },
    inject: ['reload'],
    data() {
      return {
        /**/
        loading: true,
        /**/
        tableData: [],
        /**/
        pageSize: 20,
        /**/
        totalDataNumber: 0,
        /**/
        curPage: 1,
        /**/
        searchForm: {
          search: ''
        },
        /**/
        open_edit: false,
        /**/
        curModel: {},
        /**/
        open_product: false,
        /**/
        open_audit: false,
        /**/
        open_gift: false,
      };
    },
    created() {
      /**/
      this.getTableList();
    },
    methods: {

      /**/
      searchSubmit() {
        this.curPage = 1;
        this.getTableList();
      },

      /**/
      handleCurrentChange(val) {
        let self = this;
        self.curPage = val;
        self.loading = true;
        self.getTableList();
      },

      /**/
      handleSizeChange(val) {
        this.curPage = 1;
        this.pageSize = val;
        this.getTableList();
      },

      /**/
      getTableList() {
        let self = this;
        let Params = {
          page: self.curPage,
          list_rows: self.pageSize,
          username: self.searchForm.search
        };

        liveApi.getRoom(Params, true)
          .then(data => {
            self.loading = false;
            self.tableData = data.data.list.data;
            self.totalDataNumber = data.data.list.total;
          })
          .catch(error => {});
      },

      /**/
      deleteClick(row) {
        let self = this;
        self.$confirm('?', '', {
          type: 'warning'
        }).then(() => {
          liveApi.deleteRoom({
            room_id: row.room_id
          }).then(data => {
            self.$message({
              message: '',
              type: 'success'
            });
            self.getTableList();
          });
        });
      },

      /**/
      showProduct(item) {
        this.curModel = item;
        this.open_product = true;
      },

      /**/
      closeProducts() {
        this.open_product = false;
      },

      /**/
      showGift(item) {
        this.curModel = item;
        this.open_gift = true;
      },

      /**/
      closeGift() {
        this.open_gift = false;
      },
      /**/
      editClick(e) {
        this.curModel = e;
        this.open_edit = true;
      },

      /**/
      closeEdit() {
        this.open_edit = false;
        this.getTableList();
      }

    }
  };
</script>

<style scoped="scoped">
  .picture-box {
    width: 50px;
    height: 50px;
  }

  .picture-box img {
    width: 100%;
    height: 100%;
    object-fit: fill;
  }

  .user-info img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }
</style>



