<template>
  <!--
      luoyiming
      2020-06-01
      -
    -->
  <div class="product-add pb50" v-loading="loading">
    <!---->
     <el-form size="small" :model="form" ref="form" :rules="rules" label-width="100px">
       <div class="common-form"></div>
       <el-form-item label="" prop="title"><el-input v-model="form.title" placeholder="" class="max-w460"></el-input></el-form-item>
       <el-form-item label="">
         <div>
           <el-button type="primary" @click="openUpload"></el-button>
           <img class="mt10" v-img-url="'image.file_path',form"  width="120px;" alt="" />
           <!---->
           <Upload v-if="isupload" :isupload="isupload" @returnImgs="returnImgsFunc"></Upload>
         </div>
       </el-form-item>
       <el-form-item label="">
         <el-radio-group v-model="form.status">
           <el-radio :label="1"></el-radio>
           <el-radio :label="0"></el-radio>
         </el-radio-group>
       </el-form-item>
       <el-form-item label=""><el-input type="number" v-model="form.sort" placeholder="" class="max-w460"></el-input></el-form-item>
         <el-form-item label="" prop="image_id" >
               <el-row>
                 <div class="url-box flex-1">
                   <!--  <span class="key-name"></span> -->
                     <el-input v-model="form.link_url" type="text" class="max-w460" style="width: 380px;"></el-input>
                    <el-button type="primary" @click="changeLink()"></el-button>
                    <div class="tips" id="tips" style="color: #000;">{{tips_Id}}</div>
                    <el-input v-model="form.name" type="hidden" class="max-w460" ></el-input>
                 </div>
                 <div class="url-box ml10" style="float: right;">

                 </div>
                 </el-row>
        </el-form-item>
       <Setlink v-if="is_linkset" :is_linkset="is_linkset" @closeDialog="closeLinkset"></Setlink>
       <!---->
       <div class="common-button-wrapper">
         <el-button size="small" type="info" @click="cancelFunc" :loading="loading"></el-button>
         <el-button size="small" type="primary" @click="onSubmit" :loading="loading"></el-button>
       </div>
     </el-form>
  </div>
</template>

<script>
import AdApi from '@/api/ad.js';
import Upload from '@/components/file/Upload';
import Setlink from '@/components/setlink/Setlink';
export default {
  components: {
    /**/
    Upload: Upload,
    Setlink
  },
  data() {
    return {
      /**/
      loading: true,
      /**/
      isupload: false,
      /*form*/
      is_linkset: false,
      tips_Id:'',
      form: {
        ad_id:'',
        title: '',
        category_id: 0,
        image_id: '',
        image:{},
        sort: 1,
        status: '0',
      },
      /**/
      category: [],
      /**/
      rules: {
        title: [{ required: true, message: '', trigger: 'blur' }],
      }
    };
  },
  created() {

    this.getDetail();

  },

  methods: {
    changeLink() {
    this.is_linkset = true;
    },

    /**/
    closeLinkset(e) {
          //console.log(e);
    this.is_linkset = false;
    if(e !=null){
    this.form.link_url = e.url;
    this.form.name = '' + ' ' + e.type + ' ' + e.name;
    this.tips_Id = '' + ' ' + e.type + ' ' + e.name;
     }
    },
    /**/
    openUpload() {
      this.isupload = true;
    },

    /**/
    returnImgsFunc(e) {
      let self = this;
      if (e != null) {
        this.form.image_id = e[0].file_id;
        this.form.image.file_path = e[0].file_path;
      }
      this.isupload = false;
    },

    getDetail() {
      let self = this;
      // 
      const params = self.$route.query.ad_id;
      AdApi.adDetail({ad_id: params},true).then(res => {
          self.form = res.data.detail;
          self.tips_Id = res.data.detail.name;
          if(!self.form.image){
            self.form.image={};
          }
          self.category = res.data.catgory;
          self.loading = false;
        })
        .catch(error => {});
    },

    /**/
    onSubmit() {
      let self = this;
      let params = this.form;
      // 
      AdApi.editAd(params, true)
        .then(data => {
          self.$message({
            message: data.msg,
            type: 'success'
          });
          self.$router.push('/operate/ad/index');
        })
        .catch(error => {});
    },

    /**/
    cancelFunc() {
      this.$router.push({
        path: '/operate/ad/index'
      });
    }
  }
};
</script>

<style>
.edit_container {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.ql-editor {
  height: 400px;
}
</style>



