<template>
  <!--
        luoyiming
        2020-07-25
        ---
    -->
  <el-dialog title="" :visible.sync="dialogVisible" @close="cancelFunc" :close-on-press-escape="false" width="800px">
    <!-- <div class="common-seach-wrap">
      <el-form size="small" :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label=""><el-input placeholder="" v-model="formInline.nickName"></el-input></el-form-item>
        <el-form-item>
          <el-button icon="el-icon-search" @click="search"></el-button>
        </el-form-item>
      </el-form>
    </div> -->
    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table :data="tableData" size="small" border style="width: 100%" v-loading="loading" >
          <el-table-column prop="" label="" width="70">
            <template slot-scope="scope">
              <img :src="scope.row.product.image[0].file_path" class="radius" width="30" height="30" />
            </template>
          </el-table-column>
          <el-table-column prop="product.product_name" label=""></el-table-column>
          <el-table-column prop="create_time" label="" width="140"></el-table-column>
          <!-- <el-table-column label="" width="50">
            <template slot-scope="scope">
              <el-button @click="deleteClick(scope.row)" type="text" size="small"></el-button>
            </template>
          </el-table-column> -->
        </el-table>
      </div>

      <!---->
      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          background
          :current-page="curPage"
          :page-sizes="[2, 10, 20, 50, 100]"
          :page-size="pageSize"
          layout="total, prev, pager, next, jumper"
          :total="totalDataNumber"
        ></el-pagination>
      </div>
    </div>

    <!-- <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="dialogVisible = false"> </el-button>
      <el-button size="small" type="primary" @click="confirmFunc"> </el-button>
    </div> -->
  </el-dialog>
</template>

<script>
import liveApi from '@/api/live.js';
export default {
  data() {
    return {
      /**/
      loading: true,
      /**/
      curPage: 1,
      /**/
      pageSize: 15,
      /**/
      totalDataNumber: 0,
      /**/
      formInline: {
        /**/
        grade_id: '',
        /**/
        nickName: '',
        /**/
        sex: ''
      },
      /**/
      gradeList: [],
      /**/
      tableData: [],
      /**/
      sex: ['', '', ''],
      /**/
      multipleSelection: [],
      /**/
      dialogVisible: false
    };
  },
  props: {
    is_open: Boolean,
    curModel:Object
  },
  watch: {
    is_open: function(n, o) {
      if (n != o&&n) {
         this.dialogVisible = n;
        if (n) {
          this.getTableList();
        }
      }
    }
  },
  created() {},
  methods: {


    /**/
    handleCurrentChange(val) {
      this.curPage = val;
      this.getTableList();
    },

    /**/
    handleSizeChange(val) {
      this.curPage = 1;
      this.pageSize = val;
      this.getTableList();
    },

    /**/
    getTableList() {
      let self = this;
      self.loading = true;
      let params = self.formInline;
      params.page = self.curPage;
      params.list_rows = self.pageSize;
      params.room_id = self.curModel.room_id;
      liveApi.getProduct(params,true).then(data => {
          self.loading = false;
          self.tableData = data.data.list.data;
          self.totalDataNumber = data.data.list.total;
          self.gradeList = data.data.grade;
        })
        .catch(error => {
          self.loading = false;
        });
    },

    /**/
    search() {
      this.curPage = 1;
      this.tableData = [];
      this.getTableList();
    },


    /**/
    cancelFunc(e) {
      this.$emit('close', {
        type: 'error'
      });
    },

    /**/
    deleteClick(row) {
      let self = this;
      self.$confirm('?', '', {
        type: 'warning'
      }).then(() => {
        videoApi.deleteComment({
          comment_id: row.comment_id
        }).then(data => {
          self.$message({
            message: '',
            type: 'success'
          });
          self.getTableList();
        });
      });
    }
  }
};
</script>

<style></style>



