<template>
  <!--
           luoyiming
          2020-06-09
          -
      -->
  <div class="user">
    <!---->
    <div class="common-level-rail">
      <el-button size="small" type="primary" @click="addClick" icon="el-icon-plus" v-auth="'/setting/fullreduce/add'"></el-button>
    </div>

    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table size="small" :data="tableData" border style="width: 100%" v-loading="loading">
          <el-table-column prop="active_name" label=""></el-table-column>
          <el-table-column prop="full_type" label="">
            <template slot-scope="scope">
              <span v-if="scope.row.full_type == 1"></span>
              <span v-if="scope.row.full_type == 2"></span>
            </template>
          </el-table-column>
          <el-table-column prop="full_value" label=""></el-table-column>
          <el-table-column prop="reduce_type" label="">
            <template slot-scope="scope">
              <span v-if="scope.row.reduce_type == 1"></span>
              <span v-if="scope.row.reduce_type == 2"></span>
            </template>
          </el-table-column>
          <el-table-column prop="reduce_value" label=""></el-table-column>
          <el-table-column fixed="right" label="" width="90">
            <template slot-scope="scope">
              <el-button @click="editClick(scope.row)" type="text" size="small" v-auth="'/setting/fullreduce/edit'" ></el-button>
              <el-button @click="deleteClick(scope.row)" type="text" size="small" v-auth="'/setting/fullreduce/delete'"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!---->
      <div class="pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background :current-page="curPage"
          :page-size="pageSize" layout="total, prev, pager, next, jumper"
          :total="totalDataNumber">
        </el-pagination>
      </div>
    </div>

    <!---->
    <Add v-if="open_add" :open_add="open_add" @closeDialog="closeDialogFunc($event, 'add')"></Add>

    <!---->
    <Edit v-if="open_edit" :open_edit="open_edit" :form="currentModel" @closeDialog="closeDialogFunc($event, 'edit')"></Edit>
  </div>
</template>

<script>
  import FullreduceApi from '@/api/fullreduce.js';
  import Edit from './Edit.vue';
  import Add from './Add.vue';
  import {deepClone} from '@/utils/base.js';
  export default {
    components: {
      /**/
      Edit,
      Add
    },
    data() {
      return {
        /**/
        loading: true,
        /**/
        tableData: [],
        /**/
        pageSize: 20,
        /**/
        totalDataNumber: 0,
        /**/
        curPage: 1,
        /**/
        formInline: {
          user: '',
          region: ''
        },
        /**/
        open_add: false,
        /**/
        open_edit: false,
        /**/
        currentModel: {}
      };
    },
    created() {

      /**/
      this.getTableList();

    },
    methods: {

      /**/
      handleCurrentChange(val) {
        let self = this;
        self.curPage = val;
        self.loading = true;
        self.getTableList();
      },

      /**/
      handleSizeChange(val) {
        this.curPage = 1;
        this.pageSize = val;
        this.getTableList();
      },

      /**/
      getTableList() {
        let self = this;
        let Params = {};
        Params.page = self.curPage;
        Params.list_rows = self.pageSize;
        FullreduceApi.getList(Params, true)
          .then(data => {
            self.loading = false;
            self.tableData = data.data.list.data;
            self.totalDataNumber = data.data.list.total
          })
          .catch(error => {

          });
      },

      /**/
      addClick() {
        this.open_add = true;
      },

      /**/
      editClick(item) {
        this.currentModel = deepClone(item);
        this.open_edit = true;
      },

      /**/
      closeDialogFunc(e, f) {
        if (f == 'add') {
          this.open_add = e.openDialog;
          if (e.type == 'success') {
            this.getTableList();
          }
        }
        if (f == 'edit') {
          this.open_edit = e.openDialog;
          if (e.type == 'success') {
            this.getTableList();
          }
        }
      },

      /**/
      deleteClick(row) {
        let self = this;
        self.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          self.loading = true;
          FullreduceApi.del({
              fullreduce_id: row.fullreduce_id
            }, true)
            .then(data => {
              self.loading = false;
              self.$message({
                message: data.msg,
                type: 'success'
              });
              self.getTableList();
            })
            .catch(error => {
              self.loading = false;
            });

        }).catch(() => {

        });
      }

    }
  };
</script>

<style></style>



