<template>
  <!--
    	luoyiming
    	2019-10-25
    	-
    -->
  <el-dialog title="" :visible.sync="dialogVisible" @close="dialogFormVisible" :close-on-click-modal="false"
    :close-on-press-escape="false">


    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table :data="tableData" border style="width: 100%" v-loading="loading" @current-change="handleCurrentChange">
          <el-table-column prop="name" label=""></el-table-column>
          <el-table-column prop="min_price" label=""></el-table-column>
          <el-table-column prop="total_num" label="">
            <template slot-scope="scope">
              <span v-if="scope.row.total_num>0">{{scope.row.total_num}}</span>
              <span v-else></span>
            </template>
          </el-table-column>
          <el-table-column label="" width="80">
            <template slot-scope="scope">
              <el-button type="primary" size="mini" @click="selectUser(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!---->
      <div class="pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background :current-page="curPage"
          :page-sizes="[2, 10, 20, 50, 100]" :page-size="pageSize" layout="total, prev, pager, next, jumper" :total="totalDataNumber"></el-pagination>
      </div>
    </div>

    <!-- <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="dialogFormVisible"> </el-button>
      <el-button size="small" type="primary" @click="addClerk"> </el-button>
    </div> -->
  </el-dialog>
</template>

<script>
  import CouponApi from '@/api/coupon.js';
  export default {
    data() {
      return {
        /**/
        loading: true,
        /**/
        curPage: 1,
        /**/
        pageSize: 20,
        /**/
        totalDataNumber: 0,
        formInline: {
          name: '',
        },
        //
        tableData: [],
        formRules: {
          name: [{
              required: true,
              message: '',
              trigger: 'blur'
            },
            {
              min: 3,
              max: 5,
              message: ' 3  5 ',
              trigger: 'blur'
            }
          ]
        },
        multipleSelection: [],
        /**/
        formLabelWidth: '120px',
        /**/
        dialogVisible: false
      };
    },
    props: ['open_add'],
    created() {
      this.dialogVisible = this.open_add;
      /**/
      this.getTableList();
    },
    methods: {
      /**/
      handleCurrentChange(val) {
        this.curPage = val;
        this.getTableList();
      },

      /**/
      handleSizeChange(val) {
        this.curPage = 1;
        this.pageSize = val;
        this.getTableList();
      },
      getTableList() {
        let self = this;
        let params = {};
        CouponApi.couponList(params, true)
          .then(data => {
            if (data.code == 1) {
              self.loading = false;
              self.tableData = data.data.list.data;
              self.totalDataNumber = data.data.list.total;
              self.gradeList = data.data.grade;
            } else {
              self.$message.error('');
            }
          })
          .catch(error => {});
      },


      //
      selectUser(e) {
        let self = this;
        let params = e;
        this.$emit('closeDialog', {
          type: 'success',
          openDialog: false,
          params: params
        });
      },

      /**/
      dialogFormVisible(e) {
        if (e) {
          this.$emit('closeDialog', {
            type: 'success',
            openDialog: false
          });
        } else {
          this.$emit('closeDialog', {
            type: 'error',
            openDialog: false
          });
        }
      },
    }
  };
</script>

<style></style>



