<template>
  <!--
      	luoyiming
      	2019-06-04
      	---
      -->
  <div class="product-add">
    <!--form-->
    <el-form size="small" ref="form" :model="form" label-width="200px">
      <el-tabs v-model="activeName" type="card">
        <el-tab-pane label="" name="1"></el-tab-pane>
        <el-tab-pane label="" name="2"></el-tab-pane>
      </el-tabs>
      <template v-if="activeName=='1'">
        <!---->
        <div class="common-form"></div>
        <el-form-item label="" prop="name" :rules="[{ required: true, message: ' ' }]">
          <el-input v-model="form.name" placeholder=""></el-input>
          <div class="tips">10010</div>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="form.color">
            <el-radio label="10"></el-radio>
            <el-radio label="20"></el-radio>
            <el-radio label="30"></el-radio>
            <el-radio label="40"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="form.coupon_type">
            <el-radio label="10"></el-radio>
            <el-radio label="20"></el-radio>
          </el-radio-group>
        </el-form-item>
        <div v-if="form.coupon_type == 10">
          <el-form-item label="">
            <el-input v-model="form.reduce_price" placeholder="" type="number"></el-input>
          </el-form-item>
        </div>
        <div v-else>
          <el-form-item label=" ">
            <el-input v-model="form.discount" placeholder="" type="number"></el-input>
            <div class="tips">0-109.59.50</div>
          </el-form-item>
        </div>
        <el-form-item label="">
          <el-input v-model="form.min_price" placeholder="" type="number"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="form.expire_type">
            <el-radio label="10"></el-radio>
            <el-radio label="20"></el-radio>
          </el-radio-group>
        </el-form-item>
        <div v-if="form.expire_type == 10">
          <el-form-item label="">
            <el-input v-model="form.expire_day" placeholder="" type="number"></el-input>
          </el-form-item>
        </div>
        <div v-else>
          <el-form-item label="">
            <el-date-picker v-model="form.active_time" type="daterange" align="right" unlink-panels
              value-format="yyyy-MM-dd" range-separator="" start-placeholder="" end-placeholder=""
              :picker-options="pickerOptions0"></el-date-picker>
          </el-form-item>
        </div>
        <el-form-item label=" " prop="total_num" :rules="[{ required: true, message: ' ' }]">
          <el-input v-model="form.total_num" placeholder="" type="number"></el-input>
          <div class="tips">-1</div>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="form.free_limit">
            <el-radio :label="0"></el-radio>
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
            <el-radio :label="3"></el-radio>
          </el-radio-group>
          <div class="tips"></div>
        </el-form-item>
        <el-form-item label="">
          <el-input type="number" v-model="form.sort" placeholder=""></el-input>
        </el-form-item>
      </template>
      <template v-if="activeName=='2'">
        <div class="common-form"></div>
        <el-form-item label="">
          <el-radio-group v-model="form.apply_range">
            <el-radio :label="10"></el-radio>
            <el-radio :label="20">
              <span></span>
            </el-radio>
            <el-radio :label="30">
              <span></span>
            </el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item v-if="form.apply_range==20">
          <el-button class="mb10" @click="is_product=true" type="primary" plain>
          </el-button>
          <el-table v-if="product_list.length>0" :data="product_list" max-height="400" border style="width: 100%">
            <el-table-column prop="product_id" label="ID" width="180">
            </el-table-column>
            <el-table-column prop="product_name" label="" width="180">
            </el-table-column>
            <el-table-column prop="image" label="">
              <template slot-scope="scope">
                <img v-img-url="hasImages(scope.row.product_image)" alt="" width="50px" />
              </template>
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button @click="deleteClick(scope.$index)" type="text" size="small"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-form-item>
        <el-form-item label="" v-if="form.apply_range==30">
          <el-button class="mb10" @click="is_category=true" type="primary" plain>
          </el-button>
          <div>
            <el-tag class=" mr10 mb10" type="info" v-if="category_list.first.length>0" v-for="(item,index) in category_list.first"
              :key="item.category_id">
              {{item.parent?item.parent+''+item.name:item.name}}
            </el-tag>
            <el-tag class=" mr10 mb10" type="info" v-if="category_list.second.length>0"
              v-for="(item,index) in category_list.second" :key="item.category_id">
              {{item.parent?item.parent+''+item.name:item.name}}
            </el-tag>
          </div>
        </el-form-item>
      </template>
      <!---->
      <div class="common-button-wrapper">
        <el-button type="info" size="small" @click="cancelFunc"></el-button>
        <el-button type="primary" size="small" @click="onSubmit" :loading="loading"></el-button>
      </div>
    </el-form>
    <!---->
    <Product :isproduct="is_product" :excludeIds="exclude_ids" :islist="true" @closeDialog="closeProductFunc"></Product>
    <Category :is_category="is_category" :excludeIds='category_ids' @close="closeCategoryFunc"></Category>
  </div>
</template>

<script>
  import CouponApi from '@/api/coupon.js';
  import Product from '@/components/product/Product.vue';
  import Category from '@/components/category/category.vue';
  export default {
    components: {
      Product,
      Category
    },
    data() {
      return {
        activeName: '1',
        /*form*/
        form: {
          color: '10',
          coupon_type: '10',
          expire_type: '10',
          sort: 100,
          active_time: '',
          /*0123*/
          free_limit: 0,
          apply_range: 10,
          product: [],
        },
        loading: false,
        pickerOptions0: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7; //-8.64e7
          }
        },
        prodcutData: [],
        exclude_ids: [],
        isproduct: false,
        is_category: false,
        is_product: false,
        exclude_ids: [],
        product_list: [],
        category_list: {
          second: [],
          first: []
        },
        category_ids: []
      };
    },
    created() {},

    methods: {
      /**/
      onSubmit() {
        let self = this;
        let form = self.form;
        form.product_ids = self.exclude_ids;
        form.category_list = self.category_list;
        self.$refs.form.validate(valid => {
          if (valid) {
            self.loading = true;
            CouponApi.addCoupon(form, true)
              .then(data => {
                self.loading = false;
                self.$message({
                  message: '',
                  type: 'success'
                });
                self.$router.push('/coupon/coupon/index');
              })
              .catch(error => {
                self.loading = false;
              });
          }
        });
      },
      closeCategoryFunc(e) {
        let self = this;
        self.is_category = false;
        if (e) {
          self.category_list = e;
          console.log(e)
        }
      },
      /**/
      closeProductFunc(e) {
        let self = this;
        self.is_product = e.openDialog;
        if (e.type == 'success') {
          e.params.forEach((item, index) => {
            let params = {
              product_id: item.product_id,
              product_name: item.product_name,
              product_image: item.product_image,
            }
            self.exclude_ids.push(params.product_id)
            self.product_list.push(params);
          })
        }
      },
      deleteClick(e){
        this.exclude_ids.splice(e, 1);
        this.product_list.splice(e, 1);
      },
      hasImages(e) {
        if (e) {
          return e;
        } else {
          return '';
        }
      },
      /**/
      cancelFunc() {
        this.$router.push({
          path: '/coupon/coupon/index',
        });
      },
    }
  };
</script>

<style lang="scss" scoped>
  .product-add {
    padding-bottom: 50px;
  }

  .tips {
    color: #ccc;
    border: none;
  }
</style>



