<template>
  <div>
    <!---->
    <div class="common-form"></div>
    <el-form-item label="">
      <el-radio-group v-model="form.model.is_points_gift">
        <el-radio :label="1"></el-radio>
        <el-radio :label="0"></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="">
      <el-radio-group v-model="form.model.is_points_discount">
        <el-radio :label="1"></el-radio>
        <el-radio :label="0"></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item v-if="form.model.is_points_discount == 1" label="" :rules="[{ required: true, message: ' ' }]" prop="model.max_points_discount">
      <el-input type="number" v-model="form.model.max_points_discount" class="max-w460"></el-input>
      <div class="gray9">-1</div>
    </el-form-item>

    <!---->
    <div class="common-form" v-if="form.basicSetting.is_open == 1"></div>
    <el-form-item label="" v-if="form.basicSetting.is_open == 1">
      <el-radio-group v-model="form.model.is_agent">
        <el-radio :label="0"></el-radio>
        <el-radio :label="1"></el-radio>
      </el-radio-group>
    </el-form-item>
    <template v-if="form.model.is_agent === 1">
      <el-form-item label="" v-if="form.basicSetting.is_open == 1">
        <el-radio-group v-model="form.model.is_ind_agent">
          <el-radio :label="0"></el-radio>
          <el-radio :label="1"></el-radio>
        </el-radio-group>
        <div class="gray9">({{form.basicSetting.level}})
          <span v-if="form.basicSetting.level >= 1" style="padding-left: 10px;">1({{form.agentSetting.first_money}}%)</span>
          <span v-if="form.basicSetting.level >= 2" style="padding-left: 10px;">2({{form.agentSetting.second_money}}%)</span>
          <span v-if="form.basicSetting.level >= 3" style="padding-left: 10px;">3({{form.agentSetting.third_money}}%)</span>
        </div>
      </el-form-item>
      <template v-if="form.model.is_ind_agent === 1 && form.basicSetting.is_open == 1">
        <el-form-item label="">
          <el-radio-group v-model="form.model.agent_money_type" @change="changeMoneyType">
            <el-radio :label="10"></el-radio>
            <el-radio :label="20"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-input type="number" min="0" v-model="form.model.first_money" class="max-w460">
            <template slot="prepend">
              
            </template>
            <template slot="append">
              {{ unit }}
            </template>
          </el-input>
        </el-form-item>
        <el-form-item v-if="form.basicSetting.level >= 2">
          <el-input type="number" min="0" v-model="form.model.second_money" class="max-w460">
            <template slot="prepend">
              
            </template>
            <template slot="append">
              {{ unit }}
            </template>
          </el-input>
        </el-form-item>
        <el-form-item v-if="form.basicSetting.level >= 3">
          <el-input type="number" min="0" v-model="form.model.third_money" class="max-w460">
            <template slot="prepend">
              
            </template>
            <template slot="append">
              {{ unit }}
            </template>
          </el-input>
        </el-form-item>
      </template>
    </template>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        unit: '%',
        grade_unit: '%'
      };
    },
    created() {
      if(this.form.model.alone_grade_type == '20'){
        this.grade_unit = '';
      }
      if(this.form.model.agent_money_type == '20'){
        this.unit = '';
      }
    },
    inject: ['form'],
    methods: {
      /**/
      changeMoneyType: function(val) {
        if (val == '10') {
          this.unit = '%';
        } else {
          this.unit = '';
        }
      },
	  /**/
	  changeGradeType: function(val) {
	    if (val == '10') {
	      this.grade_unit = '%';
	    } else {
	      this.grade_unit = '';
	    }
	  }
    }
  };
</script>

<style></style>



