import request from '@/utils/request'

let CouponApi = {

  /**/
  couponList(data, errorback) {
    return request._post('/supplier/coupon.coupon/index', data, errorback);
  },
  /**/
  addCoupon(data, errorback) {
    return request._post('/supplier/coupon.coupon/add', data, errorback);
  },
  /**/
  couponDetail(data, errorback) {
    return request._post('/supplier/coupon.coupon/couponDetail', data, errorback);
  },
  /**/
  editCoupon(data, errorback) {
    return request._post('/supplier/coupon.coupon/edit', data, errorback);
  },
  /**/
  deleteCoupon(data, errorback) {
    return request._post('/supplier/coupon.coupon/delete', data, errorback);
  },
  /**/
  receiveList(data, errorback) {
    return request._post('/supplier/coupon.coupon/receive', data, errorback);
  },
  /**/
  toSendCoupon(data, errorback) {
    return request._get('/supplier/coupon.coupon/SendCoupon', data, errorback);
  },
  /**/
  SendCoupon(data, errorback) {
    return request._post('/supplier/coupon.coupon/SendCoupon', data, errorback);
  },
}

export default CouponApi;



