<template>
  <div class="product-list">
    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table size="small" :data="tableData" border style="width: 100%" v-loading="loading">
         <el-table-column prop="money" label="">
           <template slot-scope="scope">
             <span class="fb red">{{scope.row.money}}</span>
           </template>
         </el-table-column>
          <el-table-column prop="pay_type" label="">
            <template slot-scope="scope">
              <span>{{scope.row.pay_type.text}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="apply_status" label="">
            <template slot-scope="scope">
                <span>{{scope.row.apply_status.text}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="create_time" label=""></el-table-column>
        </el-table>
      </div>
    </div>
    <!---->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        background
        :current-page="curPage"
        :page-size="pageSize"
        layout="total, prev, pager, next, jumper"
        :total="totalDataNumber"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import cashApi from '@/api/cash.js';
export default {
  components: {},
  data() {
    return {
      /**/
      loading: true,
      /**/
      pageSize: 10,
      /**/
      totalDataNumber: 0,
      /**/
      curPage: 1,
      /**/
      searchForm: {
        apply_status:0
      },
      /**/
      tableData: [],
      /**/
      categoryList: [
        {
          name:'',
          apply_status:10
        },
        {
          name:'',
          apply_status:20
        },
        {
          name:'',
          apply_status:30
        },
        {
          name:'',
          apply_status:40
        }
      ]
    };
  },
  created() {
    /**/
    this.getData();
  },
  methods: {
    /**/
    handleCurrentChange(val) {
      let self = this;
      self.loading = true;
      self.curPage = val;
      self.getData();
    },

    /**/
    handleSizeChange(val) {
      this.pageSize = val;
      this.getData();
    },

    /**/
    handleClick(tab, event) {
      let self = this;
      self.curPage = 1;
      self.getData();
    },

    /**/
    getData() {
      let self = this;
      let Params = self.searchForm;
      Params.page = self.curPage;
      Params.list_rows = self.pageSize;
      self.loading = true;
      cashApi.lists(Params, true)
        .then(res => {
          self.loading = false;
          self.tableData = res.data.list.data;
          self.totalDataNumber = res.data.list.total;
        })
        .catch(error => {
          self.loading = false;
        });
    },

    /**/
    onSubmit() {
      this.curPage = 1;
      this.getData();
    },

    /**/
    editClick(row) {
      this.$router.push({
        path: '/product/product/edit',
        query: {
          product_id: row.product_id,
          scene: 'edit'
        }
      });
    },

    /**/
    delClick: function(row) {
      let self = this;
      self
        .$confirm('?', '', {
          type: 'warning'
        })
        .then(() => {
          PorductApi.delProduct({
            product_id: row.product_id
          }).then(data => {
            self.$message({
              message: '',
              type: 'success'
            });
            self.getData();
          });
        });
    }
  }
};
</script>

<style></style>



