<template>
  <!--
       WuYuseng
      2019-10-26
      -
  -->
    <div>

      <!--form-->
      <el-form size="small" ref="formData" :model="formData" label-width="150px">
        <el-form-item label="" :rules="[{required: true,message: ''}]">
          <el-input v-model="formData.name" placeholder="" class="max-w460"></el-input>
        </el-form-item>
        <!-- <el-form-item label="" :rules="[{required: true,message: ''}]">
          <el-input v-model="formData.link_name" placeholder="" class="max-w460"></el-input>
        </el-form-item> -->

        <el-form-item label="" :rules="[{required: true,message: ''}]">
          <el-input v-model="formData.link_phone" placeholder="" class="max-w460" v-bind:readonly="true"></el-input>
        </el-form-item>

        <el-form-item label="" :rules="[{required: true,message: ''}]">
          <el-input v-model="formData.address" placeholder="" class="max-w460"></el-input>
        </el-form-item>

        <el-form-item label="" :rules="[{required: true,message: ''}]">
          <el-input type="textarea" v-model="formData.description" placeholder="" class="max-w460"></el-input>
        </el-form-item>

        <el-form-item label="logo" :rules="[{required: true,message: 'Logo'}]">
          <el-button @click="chooseImg('logo')">Logo</el-button>
          <div v-if="formData.logo_id!=''" class="img">
            <img :src="logo_file_path" width="100" height="100" />
          </div>
        </el-form-item>

      <!--  <el-form-item label="" :rules="[{required: true,message: ''}]">
          <el-button @click="chooseImg('business')"></el-button>
          <div v-if="formData.business_id!=''" class="img">
            <img :src="business_file_path" width="100" height="100" />
          </div>
        </el-form-item> -->

        <!---->
        <div class="common-button-wrapper">
          <el-button size="small" type="primary" @click="onSubmit" :disabled="loading"></el-button>
        </div>
      </el-form>

      <!---->
      <Upload v-if="isupload" :isupload="isupload" @returnImgs="returnImgsFunc"></Upload>
    </div>
  </template>

  <script>

    import SettingApi from '@/api/setting.js';
    import Upload from '@/components/file/Upload';
    export default{
      components:{
        Upload
      },
      data(){
        return {
          /**/
          loading:false,
          /**/
          formData:{
            name:'',
            link_name:'',
            link_phone:'',
            address:'',
            description:'',
            logo_id:0,
            //business_id:0,
          },
          /**/
          isupload:false,
          /**/
          type:null,
          logo_file_path: '',
          business_file_path: '',
        }
      },
      created() {
        this.getParams();
      },
      methods:{

        /**/
        getParams() {
          let self = this;
          SettingApi.getSupplier({}, true).then(res => {
              self.formData = res.data.model;
              self.logo_file_path = res.data.model.logo.file_path;
              self.business_file_path = res.data.model.business.file_path;
            })
            .catch(error => {

            });
        },

        /**/
        onSubmit() {
          let self = this;
          let params = this.formData;
          self.$refs.formData.validate((valid) => {
            if (valid) {
              self.loading = true;
              SettingApi.setSupplier(params, true)
                .then(data => {
                  self.loading = false;
                  self.$message({
                    message: '',
                    type: 'success'
                  });
                })
                .catch(error => {
                  self.loading = false;
                });
            }
          });

        },

        /**/
        chooseImg(e){
          this.type=e;
          this.isupload=true;
        },

        /**/
        returnImgsFunc(e){
          if (e != null && e.length > 0) {
            if(this.type == 'logo'){
              this.logo_file_path = e[0].file_path;
              this.formData.logo_id = e[0].file_id;
            }else if(this.type == 'business'){
              this.business_file_path = e[0].file_path;
              this.formData.business_id = e[0].file_id;
            }
          }
          this.isupload=false;
        }
      }
    }
  </script>

  <style>
  </style>



