<template>
  <!--
      	luoyiming
      	2019-10-24
      	-
      -->
  <div class="evaluation-detail pb50" v-loading="loading">
    <!--form-->
    <el-form size="small" ref="form" :model="form" label-width="120px">
      <!---->
      <div class="common-form"></div>
      <el-form-item label="">
        <p>{{ form.user.nickName }}</p>
      </el-form-item>
      <el-form-item label="">
        <p>{{ form.product.product_name | isNull }}</p>
      </el-form-item>
      <el-form-item label=""><img :src="path" width="120px;" v-if="isImg" :isImg="isImg" /></el-form-item>
      <el-form-item label="">
        <p>{{ form.create_time }}</p>
      </el-form-item>
      <!-- <el-form-item label="">
        <div>
          <el-button type="primary" @click="openUpload"></el-button>
          <Upload v-if="isupload" :isupload="isupload" @returnImgs="returnImgsFunc"></Upload>
        </div>
      </el-form-item> -->
      <el-form-item label="">
        <div class="d-s-c evaluation-imgs pb16">
          <div class="item" v-if="form.image.length > 0" v-for="(item, index) in form.image" :key="index"><img v-img-url="item.file_path" /></div>
        </div>
      </el-form-item>

      <el-form-item label="" v-model="form.score">
        <p v-if="form.score == 10"></p>
        <p v-if="form.score == 20"></p>
        <p v-if="form.score == 30"></p>
      </el-form-item>
      <el-form-item label="">
        <div>{{ form.content }}</div>
        <!-- <el-input type="textarea" :rows="2" v-model="form.content"></el-input> -->
      </el-form-item>
      <el-form-item label="">
        <p>{{ form.sort }}</p>
        <!-- <el-input type="number" v-model="form.sort" placeholder=""></el-input> -->
      </el-form-item>
      <el-form-item label="">
        <el-radio-group v-model="form.status">
          <el-radio :label="1"></el-radio>
          <el-radio :label="2"></el-radio>
        </el-radio-group>
      </el-form-item>
      <!---->
      <div class="common-button-wrapper">
        <el-button size="small" type="info" @click="cancelFunc"></el-button>
        <el-button size="small" type="primary" @click="onSubmit"></el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
import PorductApi from '@/api/product.js';
import Upload from '@/components/file/Upload';
export default {
  components: {
    /**/
    Upload: Upload
  },
  data() {
    return {
      /**/
      isupload: false,
      isImg: true,
      is_comment: false,
      /**/
      path: '',
      /**/
      comment_img: '',
      /*form*/
      form: {
        user: {},
        product: {}
      },
      loading: true
    };
  },
  created() {
    /**/
    this.getComment();
  },
  methods: {
    /**/
    openUpload() {
      this.isupload = true;
    },

    /**/
    getComment() {
      let self = this;
      let comment_id = this.$route.query.comment_id;
      PorductApi.getComment(
        {
          comment_id: comment_id
        },
        true
      )
        .then(data => {
          self.loading = false;
          self.form = data.data.data;
          self.path = data.data.data.product.image[0].file_path;
          self.comment_id = data.data.data.comment_id;
        })
        .catch(error => {});
    },

    /**/
    returnImgsFunc(e) {
      let self = this;
      if (e != null) {
        this.form.image_id = e[0].file_id;
        this.comment_img = e[0].file_path;
        this.is_comment = true;
      }
      this.isupload = false;
    },

    /**/
    onSubmit() {
      let self = this;
      let params = this.form;
      PorductApi.editComment(params, true)
        .then(data => {
          if (data.code == 1) {
            self.$message({
              message: '',
              type: 'success'
            });
            self.$router.push('/product/comment/index');
          } else {
            self.$message.error('');
          }
        })
        .catch(error => {});
    },

    /**/
    cancelFunc() {
      this.$router.back(-1);
    }
  }
};
</script>

<style>
.evaluation-detail {
  margin-bottom: 50px;
}
.evaluation-detail .el-form-item {
  border-bottom: 1px solid #eeeeee;
}
.evaluation-detail .el-form-item__label {
  color: #bbbbbb;
}
.evaluation-detail .evaluation-imgs .item {
  width: 100px;
  height: 100px;
  margin-right: 4px;
  border: 1px solid #d1d5dd;
}
.evaluation-detail .evaluation-imgs .item img {
  width: 98px;
  height: 98px;
  object-fit: contain;
}
</style>



