<template>
  <!--
      
  -->
  <div class="product-list">
    <!---->
    <div class="common-seach-wrap">
      <el-form size="small" :inline="true" :model="searchForm" class="demo-form-inline">
        <el-form-item label="">
          <el-select size="small" v-model="searchForm.category_id" placeholder="">
            <el-option label="" value="0"></el-option>
            <el-option v-for="(item, index) in categoryList" :key="index" :label="item.name" :value="item.category_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input size="small" v-model="searchForm.product_name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button size="small" type="primary" icon="el-icon-search" @click="onSubmit"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button size="small" type="success"  @click="reset"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!---->
    <div class="product-content">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="-1">
          <span slot="label"></span>
        </el-tab-pane>
        <el-tab-pane label="" name="1">
          <span slot="label"></span>
        </el-tab-pane>
        <el-tab-pane label="" name="0">
          <span slot="label"></span>
        </el-tab-pane>
      </el-tabs>
      <div class="table-wrap">
        <el-table size="small" :data="tableData" border style="width: 100%" v-loading="loading" ref="multipleTable">
          <el-table-column type="selection" width="55">
          </el-table-column>
          <el-table-column prop="product_name" label="" width="300px">
            <template slot-scope="scope">
              <div class="product-info">
                <div class="pic"><img v-img-url="scope.row.image[0].file_path" alt="" /></div>
                <div class="info">
                  <div class="name">{{ scope.row.product_name }}</div>
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="product_price" label="">
            <template slot-scope="scope">
              <div class="gray3">{{ scope.row.product_price }}</div>
            </template>
          </el-table-column>
          <el-table-column prop="product_stock" label=""></el-table-column>
          <el-table-column prop="sales_actual" label=""></el-table-column>
          <el-table-column prop="is_agent" label="">
            <template slot-scope="scope">
              <div class="green" v-if="scope.row.reduce_pid != null"></div>
              <div class="gray3" v-else></div>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope" v-if="scope.row.reduce_pid != null">
              <div v-for="(item,index) in scope.row.reduce_list" :key="index" :label="item" :value="index">
                <span v-if="item.full_type == 1">{{item.full_value}}</span>
                <span v-if="item.full_type == 2">{{item.full_value}}</span>
                <span v-if="item.reduce_type == 1">{{item.reduce_value}}</span>
                <span v-if="item.reduce_type == 2">{{100 - item.reduce_value}}%</span>
              </div>
            </template>
          </el-table-column>
          <el-table-column fixed="right" label="" width="80">
            <template slot-scope="scope">
              <div class="table-btn-column">
                <el-button @click="settingsClick(scope.row)" type="text" size="small"></el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <!---->
    <div class="pagination">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background
        :current-page="curPage" :page-size="pageSize" layout="total, prev, pager, next, jumper"
        :total="totalDataNumber"></el-pagination>
    </div>
    <!---->
    <Settings v-if="open_settings" :open_settings="open_settings" :productModel="productModel"
      @closeDialog="closeDialogFunc($event, 'settings')"></Settings>
  </div>
</template>

<script>
  import FullreduceApi from '@/api/fullreduce.js';
  import Settings from './dialog/Settings.vue';
  export default {
    components: {
      Settings
    },
    data() {
      return {
        /**/
        activeName: '-1',
        /**/
        activeIndex: '0',
        /**/
        loading: true,
        /**/
        pageSize: 10,
        /**/
        totalDataNumber: 0,
        /**/
        curPage: 1,
        /**/
        searchForm: {
          product_name: '',
          category_id: ''
        },
        /**/
        tableData: [],
        /**/
        categoryList: [],
        /**/
        product_count: {},
        open_settings: false,
        productModel: {}
      };
    },
    created() {
      /**/
      this.getData();
    },
    methods: {
      /**/
      handleCurrentChange(val) {
        let self = this;
        self.loading = true;
        self.curPage = val;
        self.getData();
      },

      /**/
      handleSizeChange(val) {
        this.pageSize = val;
        this.getData();
      },

      /**/
      handleClick(tab, event) {
        let self = this;
        self.curPage = 1;
        self.getData();
      },

      /**/
      getData() {
        let self = this;
        let Params = self.searchForm;
        Params.page = self.curPage;
        Params.list_rows = self.pageSize;
        Params.is_join = self.activeName;
        self.loading = true;
        FullreduceApi.product(Params, true)
          .then(data => {
            self.loading = false;
            self.tableData = data.data.list.data;
            self.categoryList = data.data.category;
            self.totalDataNumber = data.data.list.total;
            self.product_count = data.data.product_count;
          })
          .catch(error => {
            self.loading = false;
          });
      },
      /**/
      onSubmit() {
        this.curPage = 1;
        this.getData();
      },
      reset() {
        this.curPage = 1;
        this.searchForm = {
          product_name: '',
          category_id: ''
        }
        this.getData();
      },
      settingsClick(row) {
        this.productModel = {
          'product_id': row.product_id,
          'product_name': row.product_name,
          'reduce_pid': row.reduce_pid,
          'reduce_list': row.reduce_list
        };
        this.open_settings = true;
      },
      /**/
      closeDialogFunc(e, f) {
        this.open_settings = e.openDialog;
        if (e.type == 'success') {
          this.getData();
        }
      },
    }
  };
</script>

<style></style>



