<template>
  <!--
      	luoyiming
      	2020-06-01
      	--
      -->
  <div class="user">
    <!-- <div class="common-seach-wrap">
      <el-form size="small" :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="">
          <el-select v-model="formInline.is_settled" placeholder="">
            <el-option label="" value="-1"></el-option>
            <el-option label="" value="1"></el-option>
            <el-option label="" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="id"><el-input v-model="formInline.user_id" placeholder="ID"></el-input></el-form-item>
        <el-form-item><el-button type="primary" @click="onSubmit"></el-button></el-form-item>
      </el-form>
    </div> -->
    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table :data="tableData" size="small" border style="width: 100%" v-loading="loading">
          <!-- <el-table-column prop="order_master.create_time" label="">
            <template slot-scope="scope">
              <div class="product-info p-10-0" v-for="(item, index) in scope.row.order_master.product" :key="index">
                <div class="pic"><img v-img-url="item.image.file_path" alt="" /></div>
                <div class="info">
                  <div class="name gray3">{{ item.product_name }}</div>
                </div>
              </div>
            </template>
          </el-table-column> -->
          <el-table-column prop="name" label="" width="300">
          </el-table-column>
          <el-table-column prop="price" label="" width="300">
          </el-table-column>
          <el-table-column prop="card_number" label="" width="200">
          </el-table-column>
          <el-table-column prop="bank_of_deposit" label="" >
          </el-table-column>

          <el-table-column prop="type" label="" >
            <template slot-scope="scope">
              <span class="green" v-if="scope.row.type == 1"></span>
              <span  v-if="scope.row.type == 0"></span>
              <span class="red" v-if="scope.row.type == 2"></span>
            </template>
          </el-table-column>
          <el-table-column prop="create_time" label="" ></el-table-column>

          <!-- <el-table-column prop="mobile" label="" width="130">
            <template slot-scope="scope">
              <p>
                <span class="gray9"></span>
                {{ scope.row.order_master.pay_status.text }}
              </p>
              <p>
                <span class="gray9"></span>
                {{ scope.row.order_master.delivery_status.text }}
              </p>
              <p>
                <span class="gray9"></span>
                {{ scope.row.order_master.receipt_status.text }}
              </p>
            </template>
          </el-table-column> -->
          <!-- <el-table-column prop="referee.value" label="" >
            <template slot-scope="scope">
              <span class="green" v-if="scope.row.is_settled == 1"></span>
              <span class="red" v-if="scope.row.is_settled == 0"></span>
            </template>
          </el-table-column> -->

          <el-table-column fixed="right" label="" width="100">
            <template slot-scope="scope">
              <el-button @click="handleClick(scope.row)" type="text" v-if="scope.row.type == 0" size="small"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!---->
      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          background
          :current-page="curPage"
          :page-size="pageSize"
          layout="total, prev, pager, next, jumper"
          :total="totalDataNumber"
        ></el-pagination>
      </div>
    </div>
    <Tag v-if="open_tag" :open_tag="open_tag" :form="userModel" @closeDialog="closeDialogFunc($event, 'tag')"></Tag>
  </div>
</template>

<script>
 import OrderApi from '@/api/order.js';
 import Tag from '../order/dialog/Tag2.vue'
export default {
  components: {
    /**/
    Tag
  },
  data() {
    return {
      /**/
      loading: true,
      /**/
      tableData: [],
      /**/
      pageSize: 20,
      /**/
      totalDataNumber: 0,
      /**/
      curPage: 1,
      formInline: {
        is_settled: '-1',
        /*ID*/
        user_id: ''
      },
      /**/
      open_edit: false,
      /**/
      userModel: {},
      open_tag:false,
      userModel:{}
    };
  },
  props: {},
  watch: {
    $route(to, from) {
      if (to.query.user_id != null) {
        this.formInline.user_id = to.query.user_id;
      } else {
        this.formInline.user_id = '';
      }
      this.curPage = 1;
      this.getData();
    }
  },
  created() {
    if (this.$route.query.user_id != null) {
      this.formInline.user_id = this.$route.query.user_id;
    }
    /**/
    this.getData();
  },
  methods: {
    /**/
    handleCurrentChange(val) {
      let self = this;
      self.curPage = val;
      self.loading = true;
      self.getData();
    },

    /**/
    getData(user_id) {
      let self = this;
      let Params = {
        user_id: self.formInline.user_id,
        page: self.curPage,
        list_rows: self.pageSize,
        is_settled: self.is_settled
      };
      OrderApi.withdrawalList(Params, true)
        .then(data => {
          self.loading = false;
          self.tableData = data.data.data;
          console.log(self.tableData,"")
          self.totalDataNumber = data.data.total;
        })
        .catch(error => {
          self.loading = false;
        });
    },

    //
    onSubmit() {
      let self = this;
      self.loading = true;
      self.is_settled = self.formInline.is_settled;
      self.getData();
    },

    /**/
    handleSizeChange(val) {
      this.curPage = 1;
      this.pageSize = val;
      this.getData();
    },

    /**/
    editClick(item) {
      this.userModel = item;
      this.open_edit = true;
    },
    handleClick(item){
      this.userModel = item;
      this.open_tag = true;
    },
    /**/
    closeDialogFunc(e, f) {
      console.log(e,f,"")
      if (f == 'edit') {
        this.open_edit = e.openDialog;
        if (e.type == 'success') {
          this.getData();
        }
      }
      if(f == 'tag'){
        this.open_tag = false;
        if(e.type == 'success'){
          this.getData();
        }
      }
    },
  }
};
</script>

<style scoped="">
  .referee-name{ width: 33.333333%;}
</style>



