<template>
  <!--
    	luoyiming
    	2019-10-24
    	
    -->
  <div class="product">
    <!---->
    <div class="common-seach-wrap">
      <el-form size="small" :inline="true" :model="searchForm" class="demo-form-inline">
        <el-form-item label="">
          <el-select size="small" v-model="searchForm.score" placeholder="">
            <el-option label="" :value="0"></el-option>
            <el-option v-for="(item,index) in commentList" :key="index" :label="item.name" :value="item.val"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input size="small" v-model="searchForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button size="small" type="primary" icon="el-icon-search" @click="onSubmit"></el-button>
        </el-form-item>
      </el-form>
    </div>

    <template>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="-1"></el-tab-pane>
        <el-tab-pane :label="'('+num+')'" name="0"></el-tab-pane>
        <el-tab-pane label="" name="1"></el-tab-pane>
        <el-tab-pane label="" name="2"></el-tab-pane>
      </el-tabs>
    </template>

    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table  size="small" :data="tableData" border style="width: 100%" v-loading="loading">
          <el-table-column label="" width="400px">
            <template slot-scope="scope">
              <div class="product-info">
                <div class="pic"><img v-img-url="scope.row.product.image[0].file_path" alt="" /></div>
                <div class="info">
                  <div class="name">{{scope.row.product.product_name}}</div>
                  <div class="price">{{scope.row.product.product_price}}</div>
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="user.nickName" label="	"></el-table-column>
          <el-table-column prop="score" label="">
            <template slot-scope="scope">
              <span v-if="scope.row.score==10" class="red"></span>
              <span v-else-if="scope.row.score==20" class="orange"></span>
              <span v-else-if="scope.row.score==30" class="gray"></span>
            </template>
          </el-table-column>
          <el-table-column prop="content" label="" width="200">
            <template slot-scope="scope">
              <p class="ww100 text-ellipsis-2">{{scope.row.content}}</p>
            </template>
          </el-table-column>
          <el-table-column prop="is_picture" label="">
            <template slot-scope="scope">
              <span v-if="scope.row.is_picture==0"></span>
              <span v-else></span>
            </template>
          </el-table-column>
          <el-table-column prop="status" label="">
            <template slot-scope="scope">
              <span v-if="scope.row.status==0" class="red fb"></span>
              <span v-if="scope.row.status==1" class="green"></span>
              <span v-if="scope.row.status==2" class="gray9"></span>
            </template>
          </el-table-column>
          <el-table-column prop="sort" label=""></el-table-column>
          <el-table-column prop="create_time" label=""></el-table-column>
          <el-table-column fixed="right" label="" width="90">
            <template slot-scope="scope">
              <el-button @click="detailClick(scope.row)" type="text" size="small" v-auth="'/product/comment/detail'"></el-button>
              <el-button @click="delClick(scope.row)" type="text" size="small" v-auth="'/product/comment/delete'"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!---->
      <div class="pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background :current-page="curPage"
          :page-size="pageSize" layout="total, prev, pager, next, jumper"
          :total="totalDataNumber">
        </el-pagination>
      </div>
    </div>

  </div>
</template>

<script>
  import PorductApi from '@/api/product.js';
  export default {
    components: {

    },
    data() {
      return {
        loading: true,
        activeName: '-1',
        num: 0,
        /**/
        tableData: [],
        /**/
        pageSize: 20,
        /**/
        totalDataNumber: 0,
        /**/
        curPage: 1,
        /**/
        searchForm: {
          score: 0,
          name: ''
        },
        /**/
        commentList: [{
            name: '',
            val: 10
          },
          {
            name: '',
            val: 20
          },
          {
            name: '',
            val: 30
          },
        ],
        status: -1
      };
    },
    created() {
      /**/
      this.getData();
    },
    methods: {

      /**/
      handleCurrentChange(val) {
        let self = this;
        self.curPage = val;
        self.loading = true;
        let Params = self.searchForm;
        self.getData(Params);
      },

      /**/
      handleSizeChange(val) {
        this.curPage = 1;
        this.pageSize = val;
        this.getData();
      },

      /**/
      getData(param = '') {
        let self = this;
        let Params = {};
        Params.status = self.status;
        Params.page = self.curPage;
        Params.list_rows = self.pageSize;
        if (param != '') {
          Params.score = param.score;
          Params.name = param.name;
        }
        PorductApi.getCommentList(Params, true).then(res => {
          self.loading = false;
          self.tableData = res.data.list.data;
          self.totalDataNumber = res.data.list.total
          self.num = res.data.num;
        }).catch(error => {
          self.loading = false;
        });
      },

      /**/
      detailClick(row) {
        this.$router.push({
          path: '/product/comment/Detail',
          query: {
            comment_id: row.comment_id,
          }
        });
      },

      /**/
      orderClick(row) {
        this.$router.push({
          path: '/product/comment/Order',
          query: {
            order_id: row.order_id,
          }
        });
      },

      /**/
      delClick: function(row) {
        let self = this;
        self.$confirm('?', '', {
          type: 'warning'
        }).then(() => {
          PorductApi.delComment({
            comment_id: row.comment_id
          }).then(data => {
            self.$message({
              message: '',
              type: 'success'
            });
            self.getData();
          });
        });
      },

      /**/
      onSubmit() {
        let self = this;
        self.loading = true;
        let Params = self.searchForm;
        Params.page = self.curPage;
        Params.list_rows = self.pageSize;
        Params.status = self.status;
        PorductApi.getCommentList(Params, true).then(data => {
          self.loading = false;
          self.tableData = data.data.list.data;

          self.totalDataNumber = data.data.list.total
        }).catch(error => {
          self.loading = false;
        });
      },

      /**/
      handleClick(tab, event) {
        let self = this;
        self.curPage = 1;
        self.loading = true;
        self.status = tab.name;
        self.getData();
      },
    }
  };
</script>

<style></style>



