<template>
  <div class="product-add">
    <el-tabs v-model="activeName" type="card">
      <el-tab-pane label="" name="basic"></el-tab-pane>
      <el-tab-pane label="" name="spec"></el-tab-pane>
      <el-tab-pane label="" name="content"></el-tab-pane>
      <el-tab-pane label="" name="fenqi"></el-tab-pane>
      <el-tab-pane label="" name="buyset"></el-tab-pane>
    </el-tabs>
    <!--form-->
    <el-form size="small" ref="form" :model="form" label-width="180px">
      <!---->
      <Basic v-show="activeName == 'basic'"></Basic>
      <!---->
      <Spec v-show="activeName == 'spec'"></Spec>
      <!---->
      <Content v-show="activeName == 'content'"></Content>
      <!--  -->
      <fenqi v-show="activeName == 'fenqi'"></fenqi>
      <!---->
      <Buyset v-show="activeName == 'buyset'"></Buyset>
      <!---->
      <div class="common-button-wrapper">
        <el-button size="small" type="info" @click="cancelFunc"></el-button>
        <el-button size="small" type="primary" @click="onSubmit" :loading="loading"></el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
  import PorductApi from '@/api/product.js';
  import Basic from './part/Basic.vue';
  import Spec from './part/Spec.vue';
  import Content from './part/Content.vue';
  import Buyset from './part/Buyset.vue';
  import fenqi from './part/fenqi.vue';

  export default {
    components: {
      /**/
      Basic,
      /**/
      Spec,
      /**/
      Content,
      /**/
      Buyset,
      // 
      fenqi
    },
    data() {
      return {
        activeName: 'basic',
        /**/
        loading: false,
        /*form*/
        form: {
          model: {
            scene: 'add',
            /**/
            product_name: '',
            /**/
            product_no: '',
            /**/
            category_id: null,
            /**/
            image: [],
            is_picture: 0,
            contentImage: [],
            video_id: 0,
            poster_id: 0,
            /**/
            selling_point: '',
            /*,1020*/
            spec_type: 20,
            /*,2010*/
            deduct_stock_type: 20,
            sku: {},
            /**/
            spec_many: {
              /**/
              spec_attr: [],
              /**/
              spec_list: []
            },
            /**/
            content: '',
            /*ID*/
            is_delivery_free: 0,
            delivery_id: '',
            /**/
            product_status: 10,
            audit_status: 0,
            new_machine: 1,
            /*100*/
            product_sort: 100,
            /*,10*/
            is_points_gift: 1,
            /*,10*/
            is_points_discount: 1,
            /**/
            max_points_discount: 0,
            /**/
            is_agent: 0,
            /*,01*/
            is_ind_agent: 0,
            /*,1020*/
            agent_money_type: 10,
            /**/
            first_money: 0,
            /**/
            second_money: 0,
            /**/
            third_money: 0,
            /**/
            is_virtual: 0,
            /**/
            limit_num: 0,
            /**/
            virtual_auto: 0,
            /**/
            virtual_content: '',
            tableData:[]
          },
          /**/
          category: [],
          /**/
          delivery: [],
          /**/
          gradeList: [],
          /**/
          specData: null,
          /**/
          isSpecLocked: false,
          /**/
          basicSetting: {},
          /**/
          agentSetting: {},
          audit_setting: {},
          
        }
      };
    },
    provide: function() {
      return {
        form: this.form
      }
    },
    created() {
      /**/
      this.getBaseData();
    },
    methods: {
      /**/
      getBaseData: function() {
        let self = this;
        PorductApi.getBaseData({}, true)
          .then(res => {
            self.loading = false;
            Object.assign(self.form, res.data);
          })
          .catch(error => {
            self.loading = false;
          });
      },

      /*JSON*/
      convertJson(list) {
        let obj = {};
        list.forEach(item => {
          obj[item.grade_id] = item.product_equity;
        });
        return JSON.stringify(obj);
      },

      /**/
      onSubmit: function() {
        let self = this;
        let params = self.form.model;
        self.$refs.form.validate(valid => {
          if (valid) {
            if(params.is_delivery_free == 0){
              params.delivery_id = 0;
            }else{
              if(params.delivery_id == ''){
                self.$message({
                  message: '',
                  type: 'error'
                });
                return;
              }
            }
            self.loading = true;
            PorductApi.addProduct({
                params: JSON.stringify(params)
              }, true)
              .then(data => {
                self.loading = false;
                self.$message({
                  message: '',
                  type: 'success'
                });
                self.$router.push('/product/product/index');
              })
              .catch(error => {
                self.loading = false;
              });
          }else{
            self.$message({
              message: '',
              type: 'error'
            });
          }
        });
      },

      /**/
      cancelFunc() {
        this.$router.back(-1);
      },

    }
  };
</script>

<style lang="scss" scoped>
  .basic-setting-content {}

  .product-add {
    padding-bottom: 100px;
  }
</style>



