import request from '@/utils/request'

let IndexApi = {

  /**/
  base(data, errorback) {
    return request._post('/supplier/index/base', data, errorback);
  },

  /**/
  getCount(data, errorback) {
    return request._post('/supplier/index/index', data, errorback);
  },



}

export default IndexApi;



