<template>
  <div>
    <div class="table-wrap">
      <el-table :data="tableData" style="width: 100%" v-loading="loading">
        <el-table-column prop="user.nickName" label=""></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <img :src="scope.row.user.avatarUrl" width="30px" height="30px" />
          </template>
        </el-table-column>
        <el-table-column prop="gender" label="">
          <template slot-scope="scope">
            {{ scope.row.user.gender | convertSex }}
          </template>
        </el-table-column>
        <el-table-column prop="user.country" label=""></el-table-column>
        <el-table-column prop="user.province" label=""></el-table-column>
        <el-table-column prop="user.city" label=""></el-table-column>
        <el-table-column prop="create_time" label="" width="140"></el-table-column>
      </el-table>

      <!---->
      <div class="pagination">
        <el-pagination background :current-page="curPage" :page-size="pageSize" layout="total, prev, pager, next, jumper" :total="totalDataNumber"></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import FansApi from '@/api/fans.js';
export default {
  components: {},
  data() {
    return {
      /**/
      tableData: [],
      loading: true,
      /**/
      pageSize: 20,
      /**/
      totalDataNumber: 0,
      /**/
      curPage: 1
    };
  },
  created() {
    /**/
    this.getData();
  },
  methods: {
    /**/
    getData() {
      let self = this;
      let Params = {};
      Params.page = self.curPage;
      FansApi.fansList(Params, true)
        .then(data => {
          self.loading = false;
          self.tableData = data.data.list.data;
          self.totalDataNumber = data.data.list.total;
        })
        .catch(error => {
          self.loading = false;
        });
    },
    /**/
    handleCurrentChange(val) {
      let self = this;
      self.curPage = val;
      self.loading = true;
      self.getData();
    },
  }
};
</script>

<style></style>



