<template>
  <!--
          luoyiming
          2019-10-25
          
      -->
  <div class="user">
    <!---->
    <!-- <div class="common-level-rail"><el-button size="small" type="primary" icon="el-icon-plus" @click="addClick" v-auth="'/store/store/add'"></el-button></div> -->
    <!---->
    <div class="common-seach-wrap">
      <el-form size="small" :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item>
          <el-input size="small" v-model="formInline.search" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button size="small" type="primary" icon="el-icon-search" @click="searchSubmit"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table size="small" :data="tableData" border style="width: 100%" v-loading="loading">
          <el-table-column prop="user_id" label="ID" width="160"></el-table-column>
          <el-table-column prop="user.nickName" label="" ></el-table-column>
          <el-table-column label="" width="60">
            <template slot-scope="scope">
              <img v-img-url="scope.row.user.avatarUrl" width="30px" height="30px" />
            </template>
          </el-table-column>
          <el-table-column prop="newMessage.create_time" label="" width="140"></el-table-column>
          <el-table-column prop="create_time" label="" width="140"></el-table-column>
          <el-table-column fixed="right" label="" width="90">
            <template slot-scope="scope">
              <el-button @click="recordClick(scope.row)" type="text" size="small" v-auth="'/chat/chat/record'"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!---->
      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          background
          :current-page="curPage"
          :page-size="pageSize"
          layout="total, prev, pager, next, jumper"
          :total="totalDataNumber"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import ServiceApi from '@/api/service.js';
export default {
  components: {},
  inject: ['reload'],
  data() {
    return {
      /**/
      loading: true,
      /**/
      tableData: [],
      /**/
      pageSize: 20,
      /**/
      totalDataNumber: 0,
      /**/
      curPage: 1,
      /**/
      formInline: {
        search: '',
      },
      /**/
      open_add: false,
      /**/
      open_edit: false,
      /**/
      userModel: {}
    };
  },
  created() {
    /**/
    this.getTableList();
  },
  methods: {
    /**/
    searchSubmit() {
      this.curPage = 1;
      this.getTableList();
    },
    /**/
    handleCurrentChange(val) {
      let self = this;
      self.curPage = val;
      self.loading = true;
      self.getTableList();
    },

    /**/
    handleSizeChange(val) {
      this.curPage = 1;
      this.pageSize = val;
      this.getTableList();
    },

    /**/
    getTableList() {
      let self = this;
      let Params = {};
      Params.page = self.curPage;
      Params.list_rows = self.pageSize;
      Params.nickName = self.formInline.search;
      ServiceApi.chatlist(Params, true)
        .then(data => {
          self.loading = false;
          self.tableData = data.data.list.data;
          self.totalDataNumber = data.data.list.total;
        })
        .catch(error => {});
    },

    /**/
    addClick() {
      this.$router.push('/store/store/add');
    },

    /**/
    recordClick(row) {
      let self = this;
      let params = row.user_id;
      this.$router.push({
        path: '/chat/chat/record',
        query: {
          user_id: params
        }
      });
    },

    /**/
    deleteClick(row) {
      let self = this;
      self
        .$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
        .then(() => {
          ServiceApi.deleteShop(
            {
              store_id: row.store_id
            },
            true
          )
            .then(data => {
              self.$message({
                message: '',
                type: 'success'
              });
              self.getTableList();
            })
            .catch(error => {});
        })
        .catch(() => {});
    }
  }
};
</script>

<style></style>



