<template>
  <!--
           luoyiming
          2019-10-26
          -
      -->
  <div class="user">
    <div class="common-form"></div>
    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table size="small" :data="tableData" border style="width: 100%" v-loading="loading">
          <el-table-column prop="express_id" label="ID"></el-table-column>
          <el-table-column prop="express_name" label=""></el-table-column>
          <el-table-column prop="express_code" label=""></el-table-column>
        </el-table>
      </div>

      <!---->
      <div class="pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background :current-page="curPage"
          :page-size="pageSize" layout="total, prev, pager, next, jumper"
          :total="totalDataNumber">
        </el-pagination>
      </div>
    </div>

  </div>
</template>

<script>
  import SettingApi from '@/api/setting.js';
  export default {
    data() {
      return {
        /**/
        loading: true,
        /**/
        tableData: [],
        /**/
        pageSize: 20,
        /**/
        totalDataNumber: 0,
        /**/
        curPage: 1
      };
    },
    created() {

      /**/
      this.getData();

    },
    methods: {

      /**/
      handleCurrentChange(val) {
        let self = this;
        self.curPage = val;
        self.loading = true;
        self.getData();
      },

      /**/
      handleSizeChange(val) {
        this.curPage = 1;
        this.pageSize = val;
        this.getData();
      },

      /**/
      getData() {
        let self = this;
        let Params = {};
        Params.page = self.curPage;
        Params.list_rows = self.pageSize;
        SettingApi.expressList(Params, true)
          .then(data => {
            self.loading = false;
            self.tableData = data.data.list.data;
            self.totalDataNumber = data.data.list.total
          })
          .catch(error => {

          });
      },

    }
  };
</script>

<style></style>



